// 请求函数封装
import axios from "axios";
import qs from "qs";
import url from "./url";
import util from "./util";

const Axios = axios.create({
    baseURL: "",
    timeout: 0,
    responseType: "json",
    crossDomain: true,
    headers: {
        "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8",
        Code: "idm"
    },
    withCredentials: false
});

const DEFAULT_ERROR = "网络存在异常";

Axios.interceptors.response.use(response => {
    //处理IE9请求json时不能自动转化成对象的问题
    if (response.data == null && response.config.responseType === "json" && response.request.responseText != null) {
        try {
            response.data = JSON.parse(response.request.responseText);
        } catch (e) {
            // ignored
        }
    }
    return response;
},error=>{
    if(error&&error.response&&error.response.status==30001&&IDM.user.getUserLoginInfo().checkExists){
        window.location.href = IDM.url.getWebPath(IDM.user.getUserLoginInfo().loginPageUrl)
    }
    // 超出 2xx 范围的状态码都会触发该函数。
    // 对响应错误做点什么
    return Promise.reject(error);
});
// 添加请求拦截器
Axios.interceptors.request.use(function (config) {
    // debugger
    IDM.setting.applications.httpInCommonHeaderList&&IDM.setting.applications.httpInCommonHeaderList.forEach(item=>{
        var result = item.express;
        switch (item.type) {
            case "cookie":
                result = IDM.getCookie(result)
                break;
            case "localStorage":
                result = window.localStorage[result]
                break;
            case "url":
                result = IDM.url.queryObject()[result]
                break;
            case "express":
                result = IDM.express.replace(result,{
                    IDM
                },true)
                break;
            case "cache":
                if(IDM.getCookie(result)){
                    result = IDM.getCookie(result)
                }else if(window.localStorage[result]){
                    result = window.localStorage[result]
                }else if(window.sessionStorage[result]){
                    result = window.sessionStorage[result]
                }
                break;
        }
        config.headers[item.name] = result==undefined?"":result;
    })
    config.timeout=IDM.setting.applications.httpTimeout||0;
    // 在发送请求之前做些什么
    return config;
}, function (error) {
    // 对请求错误做些什么
    return Promise.reject(error);
});
Promise.prototype.done = function(fn) {
    function responseHanlder(response) {
        if (response.headers) {
            var result = response.data || {};
            //success、state、message这3个属性都存在表示是我们自己应用的程序
            if (util.isDef(result.success) && util.isDef(result.state) && util.isDef(result.message)) {
                if (result.state == "20001" && !result.message) {
                    result.message = DEFAULT_ERROR;
                }
                return fn(result);
            } else {
                return fn(result);
            }
        } else {
            var result = response;
            //success、state、message这3个属性都存在表示是我们自己应用的程序
            if (util.isDef(result.success) && util.isDef(result.state) && util.isDef(result.message)) {
                if (result.state == "20001" && !result.message) {
                    result.message = DEFAULT_ERROR;
                }
                return fn(result);
            } else {
                return fn(result);
            }
        }

    }
    let r = this.then(response => {
        return responseHanlder(response);
    });
    return r;
};

Promise.prototype.error = function(fn) {
    function errorHandler(error) {
        fn({
            capture: false,
            state: -1,
            message: DEFAULT_ERROR,
            error: error
        });
    }
    return this.catch(error => {
        errorHandler(error);
    });
};

Promise.prototype.always = function(fn) {
    this.finally(fn);
};

function get(path, params, options, rootPath) {
    if (IDM.type(options) == 'string') {
        rootPath = options;
        options = null;
    }
    path = url.getWebPath(path, rootPath);
    let opts = {
        params: params,
        paramsSerializer: function(params) {
            return qs.stringify(params, {
                arrayFormat: "repeat"
            });
        }
    };
    opts = IDM.mix(opts, options || {});
    let p = Axios.get(path, opts);
    return p;
}

function post(path, params, options, rootPath) {
    if (IDM.type(options) == 'string') {
        rootPath = options;
        options = null;
    }
    path = url.getWebPath(path, rootPath);
    let configContentType = options && options.headers && options.headers["Content-Type"] ? options.headers["Content-Type"] : "";
    if (configContentType.indexOf('application/json')==-1) {
        params = qs.stringify(params);
    }
    // if (configContentType !== "multipart/form-data") {
    //     params = qs.stringify(params);
    // }
    let opts = {};
    opts = IDM.mix(opts, options || {});
    let p = Axios.post(path, params, opts);
    return p;
}

function all(arr) {
    return axios.all(arr).then(axios.spread((...res) => {
        const list = res.map(item => item.data);
        return list;
    }))
}

function upload(path, file, params, options, rootPath) {
    if (IDM.type(options) == 'string') {
        rootPath = options;
        options = null;
    }
    let opts = {
        headers: {
            "Content-Type": "multipart/form-data"
        }
    };
    opts = IDM.mix(opts, options || {});
    var forms = new FormData();
    forms.append("file", file);
    for (let k in params) {
        forms.append(k, params[k]);
    }
    path = url.getWebPath(path, rootPath);
    let p = Axios.post(path, forms, opts);
    return p;
}

window.$$allLoadFiles = {};
window.$$allLoadFilesArray = [];

function importFiles(files) {
    let states = [];
    let loadFiles = IDM.type(files) == "array" ? files.slice(0) : [files];
    let promise = new Promise(resolve => {
        recursionLoad(() => {
            resolve(states);
        });
    });

    function recursionLoad(callback) {
        let f = loadFiles.shift();
        if (f) {
            loadFile(f)
                .then(result => {
                    states.push(result);
                    result = null;
                })
                .catch(result => {
                    importFiles.kill(result.src, true);
                    states.push(result);
                    result = null;
                })
                .finally(() => {
                    recursionLoad(callback);
                });
        } else {
            callback && callback();
        }
    }
    return promise;
}

function getFileType(url) {
    if (url != null && url.length > 0) {
        if (url.lastIndexOf("?") > 0) {
            url = url.substr(0, url.lastIndexOf("?"));
        }
        return url.substr(url.lastIndexOf(".")).toLowerCase();
    }
}

function loadFile(url) {
    let type = getFileType(url);
    let fileObj = null;
    let promise = new Promise((resolve, reject) => {
        if (!window.$$allLoadFiles[url]) {
            if (type == ".js") {
                fileObj = document.createElement("script");
                fileObj.src = url;
            } else if (type == ".css") {
                fileObj = document.createElement("link");
                fileObj.href = url;
                fileObj.type = "text/css";
                fileObj.rel = "stylesheet";
            }
            if (fileObj) {
                fileObj.__views__ = [];
                fileObj.onload = fileObj.onreadystatechange = function() {
                    if (!this.readyState || "loaded" === this.readyState || "complete" === this.readyState) {
                        window.$$allLoadFiles[url].state = "success";
                        resolve(window.$$allLoadFiles[url]);
                        _.each(window.$$allLoadFiles[url].promiseList, p => {
                            p.resolve(window.$$allLoadFiles[url]);
                        });
                    }
                };
                fileObj.onerror = function() {
                    window.$$allLoadFiles[url].state = "error";
                    _.each(window.$$allLoadFiles[url].promiseList, p => {
                        p.reject(window.$$allLoadFiles[url]);
                    });
                };
                if (!window.$$allLoadFiles[url]) {
                    window.$$allLoadFiles[url] = {
                        elem: fileObj,
                        state: "pending",
                        type: type,
                        src: url,
                        promiseList: [{ resolve: resolve, reject: reject }]
                    };
                    window.$$allLoadFilesArray.push(url);
                    document.getElementsByTagName("BODY")[0].appendChild(fileObj);
                }
            }
        } else {
            window.$$allLoadFilesArray.push(url);
            let state = window.$$allLoadFiles[url].state;
            if (state == "pending") {
                window.$$allLoadFiles[url].promiseList.push({ resolve: resolve, reject: reject });
            } else if (state == "success") {
                resolve(window.$$allLoadFiles[url]);
            } else {
                reject(window.$$allLoadFiles[url]);
            }
        }
    });
    return promise;
}
importFiles.kill = function(src, mark) {
    IDM.array.remove(window.$$allLoadFilesArray, src);
    let result = _.filter(window.$$allLoadFilesArray, url => url == src);
    if (result.length <= 0 || mark === true) {
        let f = window.$$allLoadFiles[src];
        if (f && f.elem && f.elem.parentNode) {
            f.elem.parentNode.removeChild(f.elem);
        }
        delete window.$$allLoadFiles[src];
    }
};
importFiles.has = function(src) {
    let result = _.filter(window.$$allLoadFilesArray, url => url == src);
    return result.length > 0;
};

function postSync(path, params, options, rootPath){
    return this.requestSync("post",path, params, options, rootPath);
}

function getSync(path, params, options, rootPath){
    return this.requestSync("get",path, params, options, rootPath);
}

function requestSync(type,path, params, options, rootPath){
    if (IDM.type(options) == 'string') {
        rootPath = options;
        options = null;
    }
    path = url.getWebPath(path, rootPath);
    let configContentType = options && options.contentType ? options.contentType : "";
    if (configContentType.indexOf('application/json')==-1) {
        params = qs.stringify(params);
    }

    let resultData = null;

    let opts = {
        type: type,
        url: path,
        dataType: 'json',
        async:false,
        success: function(json){
            resultData = json;
        },error : function(jqXHR, textStatus, errorThrown){
            console.log("url:" + url);
            // 状态:0-未初始化，1-正在载入，2-已经载入，3-数据进行交互，4-完成。
            console.log(jqXHR.readyState);
            // 状态码:返回的HTTP状态码，比如常见的404,500等错误代码
            console.log(jqXHR.status);
            //对应状态码的错误信息，比如404错误信息是not found,500是Internal Server Error
            console.log(jqXHR.statusText);
            //服务器响应返回的文本信息
            console.log(jqXHR.responseText);
            // 返回状态
            console.log(textStatus);
            // 服务器错误
            console.log(errorThrown);
        }
    };
    opts = IDM.mix(opts, options || {});
    opts.data = params;
    
    $.ajax(opts);

    return resultData;
}
export default {
    get,
    post,
    all,
    upload,
    importFiles,
    postSync,
    getSync,
    requestSync
};

