            :style="{ transform: `rotate(${openUserCustomizationLeftBar ? 180 : 0}deg)` }"
            @click="openUserCustomizationLeftBar = !openUserCustomizationLeftBar"
            t="1659592354438"
            class="fold-svg"
            viewBox="0 0 1024 1024"
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            p-id="17129"
