        class="idm_page_user_defined_leftbar"
      >
        <div
          class="idm_page_user_defined_allmodule_item"
          :class="item.hidden !== true ? 'hover-show-remove' : ''"
          v-for="(item, index) in originalUserCustomizationModuleObject.children"
          :key="index"
        >
          <span
            :title="
              (item.props && item.props.basicAttr && item.props.basicAttr.title) ||
              (item.props &&
                item.props.compositeAttr &&
                item.props.compositeAttr.title) ||
              item.asName
            "
          >
            {{
              (item.props && item.props.basicAttr && item.props.basicAttr.title) ||
              (item.props &&
                item.props.compositeAttr &&
                item.props.compositeAttr.title) ||
              item.asName
            }}
          </span>
          <div
            class="idm_page_user_defined_allmodule_item_op"
            :title="item.hidden === true ? '添加组件' : '移除组件'"
          >
            <svg
              t="1659636928260"
              class="user-defined-selected-svg"
              v-if="item.hidden !== true"
              viewBox="0 0 1024 1024"
              version="1.1"
              xmlns="http://www.w3.org/2000/svg"
              p-id="9243"
              width="18"
              height="18"
            >
              <path
                d="M944.78369497 156.71660696l38.8044445 32.66674931a25.36238224 25.36238224 0 0 1 3.04348577 35.76095873L486.63761776 818.67478965a76.08714794 76.08714794 0 0 1-107.20679132 9.18118218l-2.40942618-2.10507822-2.28261403-2.20652672L58.64741395 508.44212703a25.36238224 25.36238224 0 0 1-0.07608729-35.86240846l35.81168359-35.93849576a25.36238224 25.36238224 0 0 1 35.88777088-0.05072486l296.56233848 295.6239297 482.21497784-572.42897249a25.36238224 25.36238224 0 0 1 35.73559752-3.0688482z"
