<script>
//流程plumb连线
import jsplumb from "jsplumb";
//jsplumb配置信息
import { jsplumbConfig } from "../plugins/config/jsplumb";

import mixin from "./mixin";
export default {
  mixins: [mixin],
  name: "Page",
  components: { jsplumb },
  data() {
    return {
      containerStyleObject: {
        width: null,
        height: null,
        "min-height": null,
      },
      contentStyleObject: {
        width: null,
        height: null,
        overflow: null,
        "transform-origin": null,
        transform: null,
      },
      //当前主题是否已经设置过了，true代表设置过了，默认值不设置了
      themeSetObject: "",
      //当前页面的数据
      PageSettingData: {},
      dialogVisibleStatus: false,
      //是否正在隐藏中
      dialogIsHideing: false,
      //窗口的对象
      dialogObjectList: [],
      //控制中心窗口是否显示
      drawerVisible: false,
      //页面属性
      pagePropData: {},
      //页面是否已经在用户自定义状态
      pageUserDefinedStatus: false,
      //保存动画
      userDefinedSaveLoading: false,
      //是否打开组件列表
      openUserCustomizationLeftBar: false,
      //是否隐藏定制化工具栏
      hiddenCustomizationToolBar: false,
      //用户定制化的对象
      originalUserCustomizationModuleObject: { children: [] },
      //用户定制化组件选择对象
      pageUserDefinedModalVisible: false,
      //当前容器显示的packageid
      currentContainerShowPackageid: "",
      //当前容器显示的组件名称
      currentContainerShowModuleName: "",
      //当前容器显示的组件对象
      currentContainerShowModuleObject: {},
      customModuleObject: null,
      //页面接口返回结果对象
      pageInterfaceResultObject: {},
      /*
        动态渲染组件的等待消费对象，需要在各个组件回调中进行消费掉，消费完删除即可
        packageid 循环容器的id,
        allModulePackageId 循环容器动态渲染的所有组件的id,
        idmContainerId 动态渲染的循环容器的containerIndex,
        itemData 循环容器当前组的数据对象
        sendMessageKey 消息key
      */
      dynamicWaitExpendObject: [],
    };
  },
  props: {
    //页面ID
    pageid: {
      type: String,
    },
    //路由ID，为空代表是主页面
    routerId: {
      type: String,
    },
    //是否预览页面
    devpreview: {
      type: Boolean,
    },
    //是否主页面
    mainPage: {
      type: Boolean,
    },
    index: {
      type: Number,
    },
    styleObject: {
      type: Object,
      default: () => {
        return {};
      },
    },
  },
  created() {
    let that = this;
    if (this.devpreview) {
      //取缓存数据
      this.PageSettingData = JSON.parse(window.localStorage["devpreview"]);
      console.log("当前预览的缓存数据", this.PageSettingData);
      this.setCachePageData(this.PageSettingData);
      this.loadPageElement();
    } else {
      if (IDM.router.pageDataCachePool.get(this.pageid)) {
        this.PageSettingData = _.cloneDeep(IDM.router.pageDataCachePool.get(this.pageid));
        console.log(
          "从缓存池中获取到页面配置数据（同pageid情况下）",
          this.PageSettingData
        );
        this.loadPageElement();
      } else {
        this.$previewApi
          .fetchPageSettingDataForVersion({
            pageid: this.pageid,
            version: "", //当前的版本，没有则必须要返回最新的，有版本要返回对应的版本结果
            urlData: this.commonParam().urlData,
          })
          .then((res) => {
            //获取页面配置数据返回数据结果
            this.PageSettingData = res.data.data;
            this.setCachePageData(this.PageSettingData);
            console.log("从数据接口中获取页面配置数据返回数据结果", this.PageSettingData);
            this.loadPageElement();
          })
          .catch(function (error) {
            IDM.message.error("页面配置信息加载失败！message：" + error.message);
          });
      }
    }
  },
  methods: {
    pageResize(clientWH) {
      let basicAttr = this.PageSettingData.page.layout.props.basicAttr;
      if (basicAttr && basicAttr.openPageWH) {
        if (!clientWH) {
          clientWH = IDM.getClientWH();
        }
        this.containerStyleObject.width = clientWH.width + "px";
        this.containerStyleObject.height = clientWH.height + "px";
        this.containerStyleObject["min-height"] = "initial";
        let pageWH = basicAttr.pageWH || "1920,1080";
        pageWH = pageWH.split(",");
        let pageWidth = pageWH[0],
          pageHeight = pageWH[1];
        this.contentStyleObject["width"] = pageWidth + "px";
        this.contentStyleObject["height"] = pageHeight + "px";
        this.contentStyleObject["overflow"] = "hidden";
        let pageWHType = basicAttr.pageWHType || "X";
        let scaleX = 1,
          scaleY = 1,
          scaleRatio = 1;
        let bodyCss = {};
        switch (pageWHType) {
          case "X":
            scaleY = scaleX = clientWH.width / pageWidth;
            bodyCss["overflow-y"] = "auto";
            bodyCss["overflow-x"] = "hidden";
            break;
          case "Y":
            scaleY = scaleX = clientWH.height / pageHeight;
            bodyCss["overflow-y"] = "hidden";
            bodyCss["overflow-x"] = "auto";
            break;
          case "XY":
            scaleX = clientWH.width / pageWidth;
            scaleY = clientWH.height / pageHeight;
            bodyCss["overflow-y"] = "hidden";
            bodyCss["overflow-x"] = "hidden";
            break;
        }
        //$("body").css(bodyCss);
        this.$nextTick(() => {
          $("body").css(bodyCss);
        });
        this.contentStyleObject["transform"] = "scale(" + scaleX + ", " + scaleY + ")";
        this.contentStyleObject["transform-origin"] = "0 0";
      }
    },
    setCachePageData(PageSettingData) {
      let that = this;
      try {
        this.propsCompletion(
          PageSettingData.page.layout,
          PageSettingData.page.componentsMap
        );
      } catch (e) {}
      IDM.router.pageDataCachePool.put(that.pageid, _.cloneDeep(PageSettingData));
    },
    /**
     * 补全属性数据到树结构中
     * @param {*} foreachList
     * @param {*} componentsMap
     */
    propsCompletion(foreachList, componentsMap) {
      if (!(foreachList instanceof Array)) {
        foreachList = [foreachList];
      }
      for (let i = 0; i < foreachList.length; i++) {
        let module = foreachList[i];
        if (module.packageid) {
          let componentFilter = componentsMap.filter(
            (item) => item.packageid == module.packageid
          );
          if (componentFilter && componentFilter.length > 0) {
            module.props = componentFilter[0].props;
            if (componentFilter[0].svgData) {
              module.svgData = componentFilter[0].svgData;
            }
          }
        }
        if (module.children && module.children.length > 0) {
          this.propsCompletion(module.children, componentsMap);
        }
      }
    },
    async loadPageElement() {
      let react = false;
      //查找props覆盖树结构中的
      for (
        let index = 0;
        index < this.PageSettingData.page.componentsMap.length;
        index++
      ) {
        const item = this.PageSettingData.page.componentsMap[index];
        if (item.comLangue && item.comLangue.toLocaleLowerCase() == "react") {
          react = true;
        }
      }
      //加载react或者其他技术栈的lib
      await this.initReactOrOtherLib(react);
      //追加js脚本
      if (
        this.PageSettingData.page.actions &&
        this.PageSettingData.page.actions.compiled
      ) {
        var pageScriptDom = $(
          '<script type="text/javascript" id="' + this.pageid + '"/>'
        );

        $("body").append(
          pageScriptDom.html(`
       (function(){
        var exports={};
        ${this.PageSettingData.page.actions.compiled}
        var functionList = Object.keys(exports);
        for(var i=0;i<functionList.length;i++){
          window[functionList[i]] = exports[functionList[i]];
        }
       })();
       `)
        );
      }
      this.initModulePropCustomBuildCode(this.PageSettingData.page.layout);
      //把map中的数据更新下
      this.PageSettingData.page.componentsMap.forEach((item) => {
        item.env = "production";
        //追加路由ID，页面ID
        item.routerId = this.routerId;
        item.pageid = this.pageid;
        if (item.comId == "0") {
          item.idmBroadcastMessage = this.idmBroadcastMessage;
        }
      });
      //把页面数据注册到消息桥接器中（改造成支持子页面）
      this.IDM.broadcast.registerModule(this.PageSettingData, this.pageid, this.routerId);
      this.setCurrentPageTitle();
      //先加载应用信息
      await this.initAppAndUserInfo(this.PageSettingData.page.layout.props);
      //判断是否需要加载连线
      let propData = this.PageSettingData.page.layout.props.compositeAttr || {};
      if (propData.openJsPlumb === true) {
        this.initJsPlumb();
      }
      this.requestPageCommonInterfaceDataBefore(() => {
        //加载完成事件
        this.setCurrentPageTitle(true);
        if (this.mainPage) {
          this.$emit("mainPageSettingHandle", this.PageSettingData);
        }
        //加载组件样式，样式内部支持使用页面接口返回的数据
        this.initModulePropCustomBuildStyle(this.PageSettingData.page.layout);
        //组件开始加载之前执行
        this.beforeLoadingFunction();
        //基本属性渲染(页面定制大小属性)
        this.pageResize();
        //页面的属性渲染
        this.pageAttrPropHandle(this.PageSettingData.page.layout.props);
        //执行组件加载
        this.$nextTick(function () {
          this.initModuleTreeToPage(null, true, this.PageSettingData.page.layout);
        });
      });
    },
    /**
     * 组件通信：接收消息的方法
     * @param {
     *  type:"发送消息的时候定义的类型，这里可以自己用来要具体做什么，统一规定的type：linkageResult（组件联动传结果值）、linkageDemand（组件联动传需求值）、linkageReload（联动组件重新加载）
     * 、linkageOpenDialog（打开弹窗）、linkageCloseDialog（关闭弹窗）、linkageShowModule（显示组件）、linkageHideModule（隐藏组件）、linkageResetDefaultValue（重置默认值）"
     *  message:{发送的时候传输的消息对象数据}
     *  messageKey:"消息数据的key值，代表数据类型是什么，常用于表单交互上，比如通过这个key判断是什么数据"
     *  isAcross:如果为true则代表发送来源是其他页面的组件，默认为false
     * } object
     */
    idmBroadcastMessage(object) {
      if (object.type === "pageCommonInterface") {
        return;
      }
      //一定要注释，因为收到消息可能是会很频繁的，这里可以选择防抖，但实际效果可能不太好（因为改变窗口要停止了元素才会变化，所以体验效果不好，但是节约了性能）
      console.log("页面组件收到消息", object, this.PageSettingData);
      var interfaceList =
        (this.PageSettingData.page.layout.props.compositeAttr
          ? this.PageSettingData.page.layout.props.compositeAttr.interfaceList
          : []) || [];
      if (interfaceList.length == 0) {
        // callback && callback();
        return;
      }
      this.orderlyRequestInterface(
        interfaceList,
        0,
        this.InterfaceParam(),
        function () {
          console.log("页面接口已重新请求完成");
        },
        object
      );
    },
    /**
     * 会话保持功能
     * 此处专门为应用程序登录信息为session准备的（在需要登录信息永久保持的情况下才建议开启此功能）
     * 处理流程：
     * 1、用户密码登录或单点登录的时候同时后端通过jwt（用户信息+密钥）生成登录令牌并存储在前端（url参数、cookie、localStorage）
     * 2、IDM在请求获取用户信息接口后发现session掉了则会通过后端提供通过jwt重新进行登录的接口去登录，一旦发现登录成功则会二次去请求获取用户信息接口
     * 3、如果jwt登录也失效了则会执行原来的跳转登录地址
     */
    async silentReLogin(propData, isreload) {
      if (propData && propData.openSessionKeep && propData.silentReLoginUrl) {
        let relogin = false,
          cacheKeyName = IDM.setting.sessionKeep.cacheKeyName,
          cacheKeyValue = "";
        console.log("当前页面cookie信息", document.cookie);
        console.log("获取到的cookie信息：" + cacheKeyName, IDM.getCookie(cacheKeyName));
        if (window.localStorage[cacheKeyName]) {
          cacheKeyValue = window.localStorage[cacheKeyName];
        } else if (IDM.getCookie(cacheKeyName)) {
          cacheKeyValue = IDM.getCookie(cacheKeyName);
        } else if (IDM.url.queryObject()[cacheKeyName]) {
          cacheKeyValue = IDM.url.queryObject()[cacheKeyName];
        }
        await IDM.http
          .post(propData.silentReLoginUrl, {
            [cacheKeyName]: cacheKeyValue,
          })
          .then((res) => {
            let resData = res.data;
            if (resData.code == "200") {
              relogin = true;
            }
          })
          .catch(function (error) {});
        if (relogin) {
          await this.fetchUserInfo(propData, false, true, true);
        }
      }
    },
    /**
     * 获取用户信息接口
     * propData:页面属性
     * sessionKeep：是否检查使用会话保持
     */
    async fetchUserInfo(propData, sessionKeep, isreload, resetUserInfo) {
      let loginStatus = true,
        that = this;
      await this.$previewApi
        .fetchUserInfo(propData.userInfoUrl, {
          pageid: this.pageid,
          ...this.commonParam(),
        })
        .then((res) => {
          let resData = res.data;
          if (resData.code == "200" && resData.data) {
            resData = resData.data;
          } else if (resData.code != "200") {
            loginStatus = false;
          }
          //只有是主页面才会设置用户信息
          (that.mainPage || resetUserInfo) && IDM.user.setCurrentUserInfo(resData);
          //发送刷新数据
          (that.mainPage || resetUserInfo) &&
            loginStatus &&
            isreload &&
            IDM.broadcast.send(
              {
                type: "linkageReload",
                rangeModule: "",
                message: new Date().getTime(),
              },
              that.routerId
            );
        })
        .catch(function (error) {
          loginStatus = false;
        });
      if (sessionKeep && !loginStatus) {
        await this.silentReLogin(propData, isreload);
      }
    },
    /**
     * 同步方式加载应用信息和用户信息
     * 并且需要判断一下是否已经设置了登录判断，如果设置了则需要确定用户ID是否为空，为空则跳转到设置好的登录地址
     */
    async initAppAndUserInfo(propDataObject) {
      let that = this;
      let propData = propDataObject.compositeAttr;
      if (
        propData.openFetchApplicationInfo &&
        propData.applicationInfoUrl &&
        this.mainPage
      ) {
        await this.$previewApi
          .fetchApplicationInfo(propData.applicationInfoUrl, {
            pageid: this.pageid,
            ...this.commonParam(),
          })
          .then((res) => {
            let resData = res.data;
            if (resData.code == "200" && resData.data) {
              resData = resData.data;
            }
            IDM.app.setAppInfo(resData);
          })
          .catch(function (error) {});
      }
      //只会保留主页面的用户信息，如果子页面开启了只有会话丢失了且再次使用重登录功能才会去把当前用户信息重新设置
      if (propData.openFetchUserInfo && propData.userInfoUrl) {
        await this.fetchUserInfo(propData, true);
      }
      let switchUserInfoFiledValue = "";
      if (propData.switchUserInfo && propData.switchUserInfoFiledName) {
        let filedExp = propData.switchUserInfoFiledName,
          echoDataName = "userObject";
        var dataObject = { IDM: window.IDM };
        dataObject[echoDataName] = IDM.user.getCurrentUserInfo();

        if (filedExp) {
          filedExp = echoDataName + (filedExp.startsWiths("[") ? "" : ".") + filedExp;
          switchUserInfoFiledValue = window.IDM.express.replace.call(
            this,
            "@[" + filedExp + "]",
            dataObject
          );
        }
        IDM.user.setUserLoginInfo(true, propData.loginPageUrl);
      } else if (propData.switchUserInfo) {
        switchUserInfoFiledValue =
          IDM.user.getCurrentUserInfo() && IDM.user.getCurrentUserInfo().data
            ? IDM.user.getCurrentUserInfo().data.userid
            : "";
        IDM.user.setUserLoginInfo(true, propData.loginPageUrl);
      }
      //判断是否需要登录(这里暂时写死data.userid)
      if (propData.switchUserInfo && !switchUserInfoFiledValue) {
        if (propData.loginPageUrl) {
          //跳转
          window.location.href = IDM.url.getWebPath(propData.loginPageUrl);
        } else {
          window.location.href = "";
        }
      }
      //判断是否开启应用和个性化设置（每个页面都需要的）
      if (
        propData.openFetchUserInfo &&
        propData.switchUserCustomization &&
        propData.userCustomizationFetchUrl
      ) {
        let customContainerModuleId =
          propData.userCustomizationModuleContainer &&
          propData.userCustomizationModuleContainer.length > 0
            ? propData.userCustomizationModuleContainer[0].moduleId
            : "";
        //如果为空则默认为页面ID
        if (!customContainerModuleId) {
          customContainerModuleId = that.PageSettingData.id;
        }
        const originalUserCustomizationModuleObject = _.cloneDeep(
          that.getModuleTree(that.PageSettingData.page.layout, customContainerModuleId)
        );
        let newCustomModuleList = [];
        originalUserCustomizationModuleObject &&
          originalUserCustomizationModuleObject.children?.forEach((customItem) => {
            if (IDM.page.checkComponentHasAuthority(customItem)) {
              newCustomModuleList.push(customItem);
            }
          });

        that.originalUserCustomizationModuleObject = { children: newCustomModuleList };
        //获取定制化后的结果
        await this.$previewApi
          .fetchUserCustomizationInfo(propData.userCustomizationFetchUrl, {
            pageid: this.pageid,
            version: this.PageSettingData.pageBaseInfo.version,
            urlData: JSON.stringify(this.commonParam()),
          })
          .then((res) => {
            if (res && res.data && res.data.data && res.data.data.customData) {
              //定制结果不为空，用用户定制的结果去跟页面结构递归循环去比对然后对他们修改排序
              let customData =
                res.data.data.customData instanceof Object
                  ? res.data.data.customData
                  : JSON.parse(res.data.data.customData);
              //获取排序后的数组
              const userCustomModuleArray = customData.children;
              if (
                !userCustomModuleArray ||
                (userCustomModuleArray && userCustomModuleArray.length == 0)
              ) {
                return;
              }
              //比对找到组件（等于customData.packageid）下的所有数组，然后分别对他们进行数组调整
              let reSortModule = that.getModuleTree(
                that.PageSettingData.page.layout,
                customData.packageid
              );
              if (reSortModule && reSortModule.children) {
                //重新排序
                let newChildren = [],
                  allUserCustomModuleIds = [];
                userCustomModuleArray.forEach((item, index) => {
                  allUserCustomModuleIds.push(item.packageid);
                  //用户没有开启组件互换才去赋值
                  if (!propData.switchUserCustomizationCommutant) {
                    let lsmodule = that.getModuleTree(
                      that.originalUserCustomizationModuleObject,
                      item.packageid
                    );
                    lsmodule ? (lsmodule.hidden = item.hidden) : null;
                  }
                  for (let index = 0; index < reSortModule.children.length; index++) {
                    const element = reSortModule.children[index];
                    if (item.packageid == element.packageid && item.hidden !== true) {
                      element.containerIndex = item.containerIndex;
                      element.relationType = item.relationType;
                      element.relationId = item.relationId;
                      newChildren.push(element);
                      break;
                    }
                  }
                });
                //没有在个性化定制里面的组件
                for (let index = 0; index < reSortModule.children.length; index++) {
                  const element = reSortModule.children[index];
                  if (allUserCustomModuleIds.indexOf(element.packageid) == -1) {
                    newChildren.push(element);
                  }
                }
                reSortModule.children = newChildren;
              }
            }
          })
          .catch(function (error) {});
      }

      //应用主题：接口的方式    主页面才会请求的
      if (
        that.mainPage &&
        propData.openIdmTheme &&
        propData.themeFetchType == "customInterface" &&
        propData.themeInterfaceUrl
      ) {
        await this.$previewApi
          .fetchThemeInfo(propData.themeInterfaceUrl, {
            pageid: this.pageid,
            ...this.commonParam(),
          })
          .then((res) => {
            that.themeInterfaceHandle(res.data, "resultData");
          })
          .catch(function (error) {});
      } else if (
        that.mainPage &&
        propData.openIdmTheme &&
        propData.themeFetchType == "customFunction" &&
        propData.themeCustomFunction &&
        propData.themeCustomFunction.length > 0
      ) {
        //自定义处理函数
        let switchThemeValue = "";
        propData.themeCustomFunction.forEach((sitem) => {
          try {
            switchThemeValue =
              window[sitem.name] &&
              window[sitem.name].call(this, {
                ...this.commonParam(),
                customParam: sitem.param,
                _this: that,
              });
          } catch (error) {}
        });
        IDM.theme.setCurrentThemeInfo(switchThemeValue);
        that.themeSetObject = switchThemeValue;
      }
    },
    /**
     * 页面用户自定义状态切换触发事件
     * status：true 进入编辑状态   false  退出编辑状态
     * save: true 且 status的参数为 false 的时候则是需要保存状态,其他情况则是不执行保存状态
     */
    pageUserDefinedStatusChangeHandle(status = true, save = false) {
      let that = this;
      this.pageUserDefinedStatus = status;
      let propData = this.PageSettingData.page.layout.props.compositeAttr;
      this.hiddenCustomizationToolBar = propData.hiddenCustomizationToolBar !== true;
      let eleDiffPos = {},
        userDefinedDomList,
        userDefinedContainerDomList,
        moveDomBox,
        switchUserCustomizationCommutant = propData.switchUserCustomizationCommutant;
      this.openUserCustomizationLeftBar = !switchUserCustomizationCommutant;
      if (
        propData.customizationToolBarToogleEvent &&
        propData.customizationToolBarToogleEvent.length > 0
      ) {
        let resultBack = this.commonCustomEventFunHandle(
          propData,
          "customizationToolBarToogleEvent",
          {
            status,
            save,
            pageData: this.PageSettingData.page,
            routerId: that.routerId,
          }
        );
        if (resultBack === false) {
          return;
        }
      }
      //首先找出要自定义的容器组件
      /**
       * 组件范围选择返回的格式：
       * [
       *  {
       *  moduleId:packageid,
       *  moduleName:asName
       *  }
       * ]
       */
      const customContainerModuleId =
        propData.userCustomizationModuleContainer &&
        propData.userCustomizationModuleContainer.length > 0
          ? propData.userCustomizationModuleContainer[0].moduleId
          : "";
      if (!customContainerModuleId) {
        IDM.message.info("没有可以自定义的容器组件！");
        return;
      }
      //对容器组件查找
      let customModuleObject = this.getModuleTree(
        this.PageSettingData.page.layout.children,
        customContainerModuleId
      );
      if (!customModuleObject) {
        return false;
      }
      let newCustomModuleObjectList = [];
      customModuleObject &&
        customModuleObject.children?.forEach((item) => {
          if (IDM.page.checkComponentHasAuthority(item)) {
            newCustomModuleObjectList.push(item);
          }
        });

      this.customModuleObject = { children: newCustomModuleObjectList };
      const customModuleRectInfo = document
        .querySelector(`#router_page_${that.routerId}`)
        .querySelector(`#${customModuleObject.packageid}`)
        .getBoundingClientRect();

      let currentMoveDom, placeholderDom, hoverContainerDom;
      //minYDom：最接近的dom，locationStr：居于上面还是下面
      let minYDom,
        locationStr = "";
      //拖拽的移动
      !this.moduleMoveEvent
        ? (this.moduleMoveEvent = function (e) {
            e.stopPropagation();
            e.preventDefault();
            const packageid = e.target.dataset.packageid;
            //移动的时候要改变 idm_module_user_defined_movebox 的 left、top
            //用鼠标的坐标减去元素相对于鼠标的差值
            var position = {};
            position.x = e.clientX - eleDiffPos.x;
            position.y = e.clientY - eleDiffPos.y;
            moveDomBox.style.left = position.x + "px";
            moveDomBox.style.top = position.y + "px";
            //计算横向中心点坐标
            const moveDomCenterX =
                position.x + moveDomBox.getBoundingClientRect().width / 2,
              moveDomCenterY = position.y + moveDomBox.getBoundingClientRect().height / 2,
              moveDomLeftX = position.x,
              moveDomRightX = position.x + moveDomBox.getBoundingClientRect().width,
              moveDomTopY = position.y,
              moveDomBottomY = position.y + moveDomBox.getBoundingClientRect().height;
            let isCustomModuleDomOut = false;
            if (
              customModuleRectInfo.x > moveDomRightX ||
              customModuleRectInfo.x + customModuleRectInfo.width < moveDomLeftX ||
              customModuleRectInfo.y > moveDomBottomY ||
              customModuleRectInfo.y + customModuleRectInfo.height < moveDomTopY
            ) {
              isCustomModuleDomOut = true;
            }
            //对可调整的（class=idm_module_user_defined）要进行监听是否触发到元素上了，
            //有两种形式，同一容器下直接排序方式，非同一容器下直接换位或者换位插入（要取页面配置）
            //idm_module_user_defined_placeholderbox 即将要插入的位置
            //当拖拽的元素坐标最大Y轴大于当前元素的Y轴不超过10px则触发换位或排序
            //crossYDomList:Y轴有交叉的
            //topMinDom:X中心点最近、距离moveTop最小的（Y轴无交叉的）
            //bottomMinDom:X中心点最近、距离moveBottom最小的（Y轴无交叉的）
            //centerMinDomList:包含了X中心点的集合
            let crossYDomList = [],
              coverXDomList = [],
              centerCrossXDomList = [];
            userDefinedDomList &&
              userDefinedDomList.forEach((userDefinedDomItem) => {
                //先得出符合Y轴的item
                const itemRectInfo = userDefinedDomItem.parentElement.getBoundingClientRect();
                // console.log("🚀 ~ file: Page.vue ~ line 413 ~ moveEvent ~ itemRectInfo", itemRectInfo)
                const itemTopY = itemRectInfo.y,
                  itemBottomY = itemRectInfo.y + itemRectInfo.height;
                if (
                  (itemTopY <= moveDomTopY && moveDomTopY <= itemBottomY) ||
                  (itemBottomY >= moveDomBottomY && itemTopY <= moveDomBottomY)
                ) {
                  //代表顶部或底部都在当前组件范围内
                  crossYDomList.push(userDefinedDomItem.parentElement);
                }
                //顶部和底部覆盖了小组件的情况(有交叉的情况)
                if (
                  itemTopY > moveDomTopY &&
                  itemBottomY < moveDomTopY &&
                  ((itemRectInfo.x < moveDomLeftX &&
                    itemRectInfo.x + itemRectInfo.width > moveDomLeftX) ||
                    (itemRectInfo.x + itemRectInfo.width < moveDomRightX &&
                      itemRectInfo.x + itemRectInfo.width > moveDomLeftX) ||
                    (moveDomLeftX < itemRectInfo.x &&
                      itemRectInfo.x + itemRectInfo.width > moveDomLeftX &&
                      itemRectInfo.x < moveDomRightX))
                ) {
                  coverXDomList.push(userDefinedDomItem.parentElement);
                }
                if (
                  moveDomCenterX > itemRectInfo.x &&
                  moveDomCenterX < itemRectInfo.x + itemRectInfo.width
                ) {
                  centerCrossXDomList.push(userDefinedDomItem.parentElement);
                }
              });
            // console.log("🚀 ~ file: Page.vue ~ line 420 ~ moveEvent ~ userDefinedDomList", userDefinedDomList)
            // console.log("🚀 ~ file: Page.vue ~ line 410 ~ moveEvent ~ crossYDomList", crossYDomList)
            //过滤完中心点X的dom后
            let filtrXcrossYDomList = [];
            if (crossYDomList.length > 0) {
              //在Y轴有交叉的取出离中心点最近的
              crossYDomList.forEach((userDefinedDomItem) => {
                const itemRectInfo = userDefinedDomItem.getBoundingClientRect();
                if (
