          customModuleObject.children[tragetModuleIndex].containerIndex;
        customModuleObject.children[moveModuleIndex].relationType = position;
        customModuleObject.children[moveModuleIndex].relationId = tragetModuleId;

        customModuleObject.children.splice(
          tragetModuleIndex + (position == "bottom" ? 1 : 0),
          0,
          customModuleObject.children[moveModuleIndex]
        );
        customModuleObject.children.splice(
          moveModuleIndex + (tragetModuleIndex < moveModuleIndex ? 1 : 0),
          1
        );
      }
    },

