        const customContainerModuleId =
          propData.userCustomizationModuleContainer &&
          propData.userCustomizationModuleContainer.length > 0
            ? propData.userCustomizationModuleContainer[0].moduleId
            : "";
        let customContainerModule = that.getModuleTree(
          this.PageSettingData.page.layout.children,
          customContainerModuleId
        );
        customContainerModule.children.push(_.cloneDeep(item));
      }

      lsmodule ? (lsmodule.hidden = false) : null;
      that.$forceUpdate();
      //重新加载组件
      that.moduleReload(item.packageid);
      //加载完毕后还需要重新注册事件
      that.registerModuleMouseDownEvent(item.packageid);
      //重新加载组件需要修改组件的排序
      //找出当前索引的最后一个
      let lastModule;
      this.customModuleObject.children &&
        this.customModuleObject.children.forEach((module) => {
          if (
            module.containerIndex == lsmodule.containerIndex &&
            module.hidden !== true
          ) {
            lastModule = module;
          }
        });
      //要重新排序的
      if (lastModule) {
        that.changePageSettingModuleSort(item.packageid, lastModule.packageid, "bottom");
      } else {
        that.changePageSettingModuleSort(
          item.packageid,
          lsmodule.containerIndex,
          "inner"
        );
      }
    },
    /**
     * 组件替换功能
     * item:要替换的对象
     * type:替换的类型：replace 和隐藏域的组件替换， changeseats 跟当前组件换位
     */
    userDefinedModuleReplace(item, type) {
      let that = this;
      const orgcontainerIndex = that.currentContainerShowModuleObject.containerIndex;
      //把当前容器的组件移出，如果type=replace 则和999999999的容器序号替换，反之和当前容器的进行替换
      that.currentContainerShowModuleObject.containerIndex = item.containerIndex;
      that.currentContainerShowModuleObject.relationType = item.relationType;
      that.currentContainerShowModuleObject.relationId = item.relationId;
      //把当前选中的组件移到当前组件所在的容器位置
      that.changePageSettingModuleSort(item.packageid, orgcontainerIndex, "inner");
      item.relationType = "inner_" + orgcontainerIndex;

      if (type == "changeseats") {
        //需要先移除被换位的，然后再渲染
        $(
          document
            .querySelector(`#router_page_${that.routerId}`)
            .querySelector(`#${item.packageid}`)
        ).remove();
      }
      //把要当前替换的全部移除，然后再渲染
      $(
        document
          .querySelector(`#router_page_${that.routerId}`)
          .querySelector(`#${that.currentContainerShowModuleObject.packageid}`)
      ).remove();
      //重新加载组件
      that.moduleReload(item.packageid);
      //加载完毕后还需要重新注册事件
      that.registerModuleMouseDownEvent(item.packageid);
      if (type == "changeseats") {
        //重新加载组件
        that.moduleReload(that.currentContainerShowModuleObject.packageid);
        //加载完毕后还需要重新注册事件
        that.registerModuleMouseDownEvent(
          that.currentContainerShowModuleObject.packageid
        );
      }
      that.pageUserDefinedModalVisible = false;
      //发送组件消息
      IDM.broadcast.send({
        type: "resendAutoLayout",
        rangeModule: "",
        message: {},
      });
    },
    /**
     * 用户个性化定制保存
     */
    userDefinedSave() {
      let that = this;
      let propData = this.PageSettingData.page.layout.props.compositeAttr;

      if (
        propData.customizationToolBarSaveEvent &&
        propData.customizationToolBarSaveEvent.length > 0
      ) {
        let resultBack = this.commonCustomEventFunHandle(
          propData,
          "customizationToolBarSaveEvent",
          {
            pageData: this.PageSettingData.page,
            routerId: that.routerId,
            pageid: this.pageid,
            version: this.PageSettingData.pageBaseInfo.version,
          }
        );
        if (resultBack === false) {
          return;
        }
      }
      that.userDefinedSaveLoading = true;

      const customContainerModuleId =
        propData.userCustomizationModuleContainer &&
        propData.userCustomizationModuleContainer.length > 0
          ? propData.userCustomizationModuleContainer[0].moduleId
          : "";

      let customModuleObject = this.getModuleTree(
        this.PageSettingData.page.layout.children,
        customContainerModuleId
      );
      if (!customModuleObject) {
        that.userDefinedSaveLoading = false;
        return false;
      }

      let saveNewData = _.cloneDeep(customModuleObject);
      this.deleteLayoutAttachAttr(saveNewData);
      //保存到数据库中
      propData.userCustomizationSaveUrl &&
        IDM.http
          .post(propData.userCustomizationSaveUrl, {
            pageid: this.pageid,
            version: this.PageSettingData.pageBaseInfo.version,
            customData: JSON.stringify(saveNewData),
          })
          .then((res) => {
            res = res ? res.data : {};
            that.userDefinedSaveLoading = false;
            if (res.code !== "200") {
              IDM.message.error("个性化定制保存失败！message：" + res.message);
              return;
            }
            IDM.message.success("保存成功！");
            //保存成功
            IDM.page.changePageUserDefinedStatus({
              routerId: that.routerId,
              status: false,
            });
          })
          .catch((error) => {
            IDM.message.error("个性化定制保存失败！message：" + error.message);
            that.userDefinedSaveLoading = false;
          });
    },
    /**
     * 用户个性化定制重置
     */
    userDefinedReset() {
      let that = this;
      let propData = this.PageSettingData.page.layout.props.compositeAttr;

      if (
        propData.customizationToolBarResetEvent &&
        propData.customizationToolBarResetEvent.length > 0
      ) {
        let resultBack = this.commonCustomEventFunHandle(
          propData,
          "customizationToolBarResetEvent",
          {
            pageData: this.PageSettingData.page,
            routerId: that.routerId,
            pageid: this.pageid,
            version: this.PageSettingData.pageBaseInfo.version,
          }
        );
        if (resultBack === false) {
          return;
        }
      }

      that.userDefinedSaveLoading = true;

      //保存到数据库中
      propData.userCustomizationSaveUrl &&
        IDM.http
          .post(propData.userCustomizationSaveUrl, {
            pageid: this.pageid,
            version: this.PageSettingData.pageBaseInfo.version,
            reset: 1,
          })
          .then((res) => {
            res = res ? res.data : {};
            if (res.code !== "200") {
              that.userDefinedSaveLoading = false;
              IDM.message.error("重置操作失败！message：" + res.message);
              return;
            }
            IDM.message.success("重置成功！3秒后即将刷新本页面。");
            setTimeout(() => {
              window.location.reload();
            }, 3000);
          })
          .catch((error) => {
            IDM.message.error("重置操作失败！message：" + error.message);
            that.userDefinedSaveLoading = false;
          });
    },
    /**
     * 用户个性化定制退出
     */
    userDefinedQuit() {
      let that = this;
      let propData = this.PageSettingData.page.layout.props.compositeAttr;
      if (
        propData.customizationToolBarQuitEvent &&
        propData.customizationToolBarQuitEvent.length > 0
      ) {
        let resultBack = this.commonCustomEventFunHandle(
          propData,
          "customizationToolBarQuitEvent",
          {
            pageData: this.PageSettingData.page,
            routerId: that.routerId,
            pageid: this.pageid,
            version: this.PageSettingData.pageBaseInfo.version,
          }
        );
        if (resultBack === false) {
          return;
        }
      }
      //不保存当前的数据，重新刷新当前页面
      if (this.isChangeUserDefined === true) {
        window.location.reload();
      } else {
        IDM.page.changePageUserDefinedStatus({ routerId: this.routerId, status: false });
      }
    },
    /**
     * 遮罩层直接关闭窗口
     */
    layerClickHandle(item, e) {
      let dialogId = item ? item.packageid : "";
      if (!dialogId) {
        return;
      }
      //获取shadeClose是否支持关闭
      if (
        item &&
        item.props &&
        item.props.compositeAttr &&
        item.props.compositeAttr.shadeClose === false
      ) {
        return;
      }
      window.IdmBuiltin_CloseDialog({
        routerId: this.routerId,
        targetModule: [{ moduleId: dialogId }],
      });
    },
    /**
     * 动态检查是否加载react文件或其他文件
     */
    async initReactOrOtherLib(react) {
      //判断是否已经加载过
      if (react && !IDM.router.getReact()) {
        IDM.router.setReact();
        //动态加载react文件
        await IDM.http
          .importFiles([
            IDM.url.getWebPath("~/lib/react.min.js"),
            IDM.url.getWebPath("~/lib/react-dom.min.js"),
          ])
          .then(() => {
            //加载完成
            console.log("react文件加载成功");
          })
          .catch((err) => {
            console.log("react文件加载出错");
          });
      }
    },
    /**
     * 组件加载之前执行
     */
    beforeLoadingFunction() {
      let propData = this.PageSettingData.page.layout.props.compositeAttr;
      if (propData.beforeLoadingFunction && propData.beforeLoadingFunction.length > 0) {
        this.commonCustomEventFunHandle(propData, "beforeLoadingFunction");
      }
    },
    /**
     * 组件加载完后执行
     */
    afterLoadingFunction() {
      let propData = this.PageSettingData.page.layout.props.compositeAttr;
      if (propData.afterLoadingFunction && propData.afterLoadingFunction.length > 0) {
        this.commonCustomEventFunHandle(propData, "afterLoadingFunction");
      }
    },
    /**
    页面请求组件之前加载接口
     */
    requestPageCommonInterfaceDataBefore(callback) {
      var interfaceList =
        (this.PageSettingData.page.layout.props.compositeAttr
          ? this.PageSettingData.page.layout.props.compositeAttr.interfaceList
          : []) || [];
      if (interfaceList.length == 0) {
        callback && callback();
        return;
      }
      interfaceList = interfaceList.filter(
        (item) => item.loadTimingType == "pageloadbefore"
      );
      if (interfaceList.length == 0) {
        callback && callback();
        return;
      }
      this.orderlyRequestInterface(interfaceList, 0, this.InterfaceParam(), callback);
    },
    /**
     * 请求页面设置的所有接口并回调给每个组件
     * @param {*} checkFirstLoad 判断是否需要进行首次加载后续不加载的判断
     */
    requestPageCommonInterfaceData(checkFirstLoad) {
      //调用桥接器页面加载(包括实时渲染)完成事件
      this.IDM.broadcast.pageLoadCompleteAfter(this.routerId);
      if (checkFirstLoad && this.pageCommonInterfaceLoaded) {
        return;
      }
      this.pageCommonInterfaceLoaded = true;
      const linkList = this.PageSettingData.page.linkList || [];
      if (linkList.length) {
        //判断是否需要加载连线
        let propData = this.PageSettingData.page.layout.props.compositeAttr || {};
        if (propData.openJsPlumb === true) {
          //加载完成了
          this.reconnectionAllModule();
        }
      }
      console.log("页面首屏组件已全部加载完毕！！！");
      this.afterLoadingFunction();

      const interfaceList = (
        this.PageSettingData.page.layout.props.compositeAttr?.interfaceList ?? []
      ).filter((item) => !item.loadTimingType || item.loadTimingType === "pageloadlater");
      if (!interfaceList.length) {
        return;
      }
      this.orderlyRequestInterface(interfaceList, 0, this.InterfaceParam());
    },
    /**
    接口请求的所有参数 */
    InterfaceParam() {
      //先获取页面ID，然后再获取所有的组件ID
      var pageId = this.pageid;
      var moduleIdArray = [],
        componentIdArray = [];
      this.PageSettingData.page.componentsMap.forEach((item) => {
        if (item.packageid != pageId) {
          moduleIdArray.push(item.packageid);
          componentIdArray.push(item.comId);
        }
      });
      //所有地址的url参数转换
      var params = {
        idmPageId: pageId,
        moduleIds: moduleIdArray.join(","),
        componentIds: componentIdArray.join(","),
      };
      let urlObject = IDM.url.queryObject();
      Object.keys(urlObject).forEach((key) => {
        params[key] = urlObject[key];
      });
      return params;
    },
    /**
     * 有序的加载接口并且回调各个组件
     */
    orderlyRequestInterface(interfaceList, index, params, callback, linkMessageObject) {
      if (interfaceList.length <= index) {
        callback && callback();
        return;
      }
      var item = interfaceList[index];
      index = index + 1;
      let that = this;
      //linkMessageObject不为空则去判断是否需要重新请求接口
      if (linkMessageObject) {
        //根据过滤条件进行匹配
        let _isPass = false;
        try {
          if (item.reloadType == "express") {
            _isPass = IDM.express.replace("@[" + item.reloadExpression + "]", {
              IDM: IDM,
              window,
              linkMessageObject,
            });
          } else if (
            item.reloadType == "customFunction" &&
            item.reloadCustomFunction &&
            item.reloadCustomFunction.length > 0
          ) {
            let itemParam = {
              ...that.commonParam(),
              ...item.reloadCustomFunction[0].param,
              linkMessageObject,
            };
            _isPass =
              window[item.reloadCustomFunction[0].name] &&
              window[item.reloadCustomFunction[0].name].call(this, itemParam);
          }
        } catch (error) {}
        if (!_isPass) {
          that.orderlyRequestInterface(
            interfaceList,
            index,
            params,
            callback,
            linkMessageObject
          );
          return;
        }
      }
      // preAllowFunction
      if (item.preAllowFunction && item.preAllowFunction.length > 0) {
        try {
          let itemParam = {
            ...that.commonParam(),
            ...item.preAllowFunction[0].param,
            linkMessageObject,
          };
          let _isPass =
            window[item.preAllowFunction[0].name] &&
            window[item.preAllowFunction[0].name].call(this, itemParam);
          if (_isPass === false) {
            that.orderlyRequestInterface(
              interfaceList,
              index,
              params,
              callback,
              linkMessageObject
            );
            return;
          }
        } catch (error) {}
      }

      //请求数据源的方式
      if (item.formDataFunction && item.formDataFunction.length > 0) {
        try {
          let itemParam = {
            ...that.commonParam(),
            ...item.formDataFunction[0].param,
            linkMessageObject,
          };
          let formDataParam =
            window[item.formDataFunction[0].name] &&
            window[item.formDataFunction[0].name].call(this, itemParam);
          params = { ...params, ...formDataParam };
        } catch (error) {}
      }

      if (
        item.interfaceType == "datasource" &&
        item.dataSourceSelectData &&
        item.dataSourceSelectData.length
      ) {
        IDM.datasource.request(
          item.dataSourceSelectData[0].id,
          {
            moduleObject: {},
            param: params,
            linkMessageObject,
          },
          function (resData) {
            //这里是请求成功的返回结果
            that.callbackModuleSetContext(item, resData);
            that.orderlyRequestInterface(
              interfaceList,
              index,
              params,
              callback,
              linkMessageObject
            );
          },
          function (error) {
            //这里是请求失败的返回结果
            that.orderlyRequestInterface(
              interfaceList,
              index,
              params,
              callback,
              linkMessageObject
            );
          }
        );
      } else if (item.interfaceMode == "post" && item.interfaceUrl) {
        this.$previewApi
          .postPageInterfaceInfo(item.interfaceUrl, params, {
            headers: item.interfaceContentType
              ? {
                  "Content-Type":
                    item.interfaceContentType || "application/json;charset=UTF-8",
                }
              : {},
          })
          .then((res) => {
            //res.data
            //循环调用组件接收数据的方法
            that.callbackModuleSetContext(item, res.data || res);
            that.orderlyRequestInterface(
              interfaceList,
              index,
              params,
              callback,
              linkMessageObject
            );
          })
          .catch(function (error) {
            that.orderlyRequestInterface(
              interfaceList,
              index,
              params,
              callback,
              linkMessageObject
            );
          });
      } else if (item.interfaceMode == "get" && item.interfaceUrl) {
        this.$previewApi
          .getPageInterfaceInfo(item.interfaceUrl, params)
          .then((res) => {
            //res.data
            //循环调用组件接收数据的方法
            that.callbackModuleSetContext(item, res.data || res);
            that.orderlyRequestInterface(
              interfaceList,
              index,
              params,
              callback,
              linkMessageObject
            );
          })
          .catch(function (error) {
            that.orderlyRequestInterface(
              interfaceList,
              index,
              params,
              callback,
              linkMessageObject
            );
          });
      } else {
        that.orderlyRequestInterface(
          interfaceList,
          index,
          params,
          callback,
          linkMessageObject
        );
      }
    },
    /**
     * 回调组件的设置值的方法
     */
    callbackModuleSetContext(item, resData) {
      //设置页面统一接口结果到data中,等接口全部请求完后再发送消息给每个组件
      this.pageInterfaceResultObject[item.resultDataName] = resData;
      this.sendBroadcastMessage({
        type: "pageCommonInterface",
        message: resData,
        messageKey: item.resultDataName,
        rangeModule: "",
      });
      if (item.loadTimingType != "pageloadbefore") {
        //加载之后执行
        this.PageSettingData.page.componentsMap.forEach((sitem) => {
          sitem.idmSetContextValue &&
            sitem.idmSetContextValue({
              type: "pageCommonInterface",
              key: item.resultDataName,
              data: resData,
            });
        });
      }
      //自定义处理返回结果函数
      if (item.interfaceRunLaterFunction && item.interfaceRunLaterFunction.length > 0) {
        var interfaceRunLaterFunction = item.interfaceRunLaterFunction;
        interfaceRunLaterFunction.forEach((sitem) => {
          try {
            window[sitem.name] &&
              window[sitem.name].call(this, {
                ...this.commonParam(),
                customParam: sitem.param,
                resultData: resData,
                _this: this,
              });
          } catch (error) {}
        });
      }
      let propData = this.PageSettingData.page.layout.props.compositeAttr || {};
      //应用主题：页面结果集
      if (
        this.mainPage &&
        propData.openIdmTheme &&
        propData.themeFetchType == "pageInterfaceResult" &&
        propData.themeDataName &&
        item.resultDataName == propData.themeDataName
      ) {
        this.themeInterfaceHandle(resData, propData.themeDataName);
      }
    },
    /**
     * 渲染页面的样式
     */
    pageAttrPropHandle(propDataObject) {
      let propData = propDataObject.compositeAttr;
      this.pagePropData = propData;
      var styleObject = {};
      if (propData.bgSize && propData.bgSize == "custom") {
        styleObject["background-size"] =
          (propData.bgSizeWidth
            ? propData.bgSizeWidth.inputVal + propData.bgSizeWidth.selectVal
            : "auto") +
          " " +
          (propData.bgSizeHeight
            ? propData.bgSizeHeight.inputVal + propData.bgSizeHeight.selectVal
            : "auto");
      } else if (propData.bgSize) {
        styleObject["background-size"] = propData.bgSize;
      }
      if (propData.positionX && propData.positionX.inputVal) {
        styleObject["background-position-x"] =
          propData.positionX.inputVal + propData.positionX.selectVal;
      }
      if (propData.positionY && propData.positionY.inputVal) {
        styleObject["background-position-y"] =
          propData.positionY.inputVal + propData.positionY.selectVal;
      }
      for (const key in propData) {
        if (propData.hasOwnProperty.call(propData, key)) {
          const element = propData[key];
          if (!element && element !== false) {
            continue;
          }
          switch (key) {
            case "defaultIdmTheme":
              if (!this.themeSetObject && this.mainPage) {
                IDM.theme.setCurrentThemeInfo(element);
                this.themeSetObject = element;
              }
              break;
            case "width":
            case "height":
              styleObject[key] = element;
              break;
            case "bgColor":
              if (element.hex8) {
                styleObject["background-color"] = IDM.hex8ToRgbaString(element.hex8);
              }
              break;
            case "box":
              IDM.style.setBoxStyle(styleObject, element);
              break;
            case "bgImgUrl":
              styleObject["background-image"] = `url(${IDM.url.getWebPath(element)})`;
              break;
            case "icoImgUrl":
              try {
                document.head
                  .querySelector("link[rel='icon']")
                  .setAttribute("href", IDM.url.getWebPath(element));
              } catch (error) {}
              //IDM.url.getWebPath(element)
              var link = document.createElement("link");
              link.type = "image/x-icon";
              link.rel = "shortcut icon";
              link.href = IDM.url.getWebPath(element);
              document.getElementsByTagName("head")[0].appendChild(link);
              break;
            case "positionX":
              //背景横向偏移

              break;
            case "positionY":
              //背景纵向偏移

              break;
            case "bgRepeat":
              //平铺模式
              styleObject["background-repeat"] = element;
              break;
            case "bgAttachment":
              //背景模式
              styleObject["background-attachment"] = element;
              break;
            case "border":
              IDM.style.setBorderStyle(styleObject, element);
              break;
            case "font":
              IDM.style.setFontStyle(styleObject, element);
              break;
            case "layout":
              IDM.style.setLayoutStyle(styleObject, element);
              break;
            case "pageMinWidth":
              if (element.inputVal && element.selectVal) {
                styleObject["min-width"] = element.inputVal + element.selectVal;
              }
              break;
          }
        }
      }
      //滤镜样式设置
      let filterStyleObject = {};
      IDM.style.setFilterStyle(filterStyleObject, propDataObject.basicAttr, {
        opacity: "pageOpacity",
      });
      IDM.setStyleToPageHead("router_page_" + this.routerId, filterStyleObject);

      this.$nextTick(function (params) {
        if (propData.watermarkType && propData.watermarkType != "none") {
          var _watermarkFont = this.$replace(propData.watermarkFont, {
              IDM: IDM,
            }),
            _watermarkFont1 = this.$replace(propData.watermarkFont1, {
              IDM: IDM,
            });
          //文字水印
          IDM.watermark.set(
            this.$refs.idm_page_root,
            propData.watermarkZindex,
            propData.watermarkType != "font" ? propData.watermarkImg : _watermarkFont,
            propData.watermarkType == "image" ? "" : _watermarkFont1,
            {
              fontColor:
                propData.watermarkColor &&
                IDM.hex8ToRgbaString(propData.watermarkColor.hex8),
              fontSize: `${propData.watermarkFontSize}px`,
              width: propData.watermarkSize,
              height: propData.watermarkSize,
              rotate: propData.watermarkRotate,
              imgSize: propData.watermarkImgSize,
              imgLeftSize: propData.watermarkImgLeftSize,
              fontLeftSize: propData.watermarkFontLeftSize,
              opacity: propData.watermarkOpacity / 100,
              topSize: propData.watermarkTopSize,
            }
          );
        } else {
          IDM.watermark.close(this.$refs.idm_page_root);
        }
      });
      IDM.setStyleToPageHead(
        "PAGE_" + this.PageSettingData.id + "_" + this.routerId,
        styleObject
      );
      //加载外部js和css
      if (propData.resourceList && propData.resourceList.length > 0) {
        propData.resourceList.forEach((item) => {
          if (item.resourceType == "js") {
            IDM.module.loadJs(
              IDM.url.getWebPath(item.resourceUrl),
              true,
              function () {},
              { async: item.async, defer: item.defer }
            );
          } else {
            IDM.module.loadCss(
              IDM.url.getWebPath(item.resourceUrl),
              true,
              function () {}
            );
          }
        });
      }
      this.convertThemeListAttrToStyleObject(propData);
    },

    /**
     * 主题颜色
     */
    convertThemeListAttrToStyleObject(propData) {
      var themeList = propData.themeList;
      if (!themeList) {
        return;
      }
      const themeNamePrefix =
        IDM.setting &&
        IDM.setting.applications &&
        IDM.setting.applications.themeNamePrefix
          ? IDM.setting.applications.themeNamePrefix
          : "idm-theme-";
      for (var i = 0; i < themeList.length; i++) {
        var item = themeList[i];
        //item.key：为主题样式的key
        //item.mainColor：主要颜色值
        //item.minorColor：次要颜色值
        if (item.key != IDM.theme.getCurrentThemeInfo()) {
          //此处比对是不渲染输出不用的样式，如果页面会刷新就可以把此处放开
          continue;
        }
        let cssObject_color_main = {
          color: item.mainColor ? item.mainColor.hex8 + " !important" : "",
        };
        let cssObject_border_main = {
          "border-color": item.mainColor ? item.mainColor.hex8 : "",
        };
        let cssObject_background_main = {
          "background-color": item.mainColor ? item.mainColor.hex8 : "",
        };
        let cssObject_color_minor = {
          color: item.minorColor ? item.minorColor.hex8 : "",
        };
        let cssObject_border_minor = {
          "border-color": item.minorColor ? item.minorColor.hex8 : "",
        };
        let cssObject_background_minor = {
          "background-color": item.minorColor ? item.minorColor.hex8 : "",
        };
        IDM.setStyleToPageHead(
          `.${themeNamePrefix + item.key} .idm_module_user_defined`,
          {
            ...cssObject_border_main,
          }
        );
        IDM.setStyleToPageHead(
          `.${themeNamePrefix + item.key} .idm_module_user_defined>div`,
          {
            ...cssObject_background_main,
          }
        );
        IDM.setStyleToPageHead(
          `.${themeNamePrefix + item.key} .user-defined-logo-svg,
          .${
            themeNamePrefix + item.key
          } .idm_page_user_defined_leftbar svg.user-defined-add-svg,
          .${
            themeNamePrefix + item.key
          } .idm_page_user_defined_modal_moduleitem .user-defined-modal-changeseats,
          .${
            themeNamePrefix + item.key
          } .idm_page_user_defined_modal_moduleitem .user-defined-modal-replace`,
          {
            ...cssObject_color_main,
          }
        );
        IDM.setStyleToPageHead(
          `.${themeNamePrefix + item.key} .idm-ant-btn:active,
          .${themeNamePrefix + item.key} .idm-ant-btn.active,
          .${themeNamePrefix + item.key} .idm-ant-btn-dashed:active,
          .${themeNamePrefix + item.key} .idm-ant-btn-dashed.active`,
          {
            ...cssObject_color_main,
            ...cssObject_border_main,
          }
        );
        IDM.setStyleToPageHead(
          `.${themeNamePrefix + item.key} .idm-ant-btn-primary,
          .${themeNamePrefix + item.key} .idm-ant-btn-primary:hover,
          .${themeNamePrefix + item.key} .idm-ant-btn-primary:focus`,
          {
            ...cssObject_background_main,
            ...cssObject_border_main,
          }
        );
        IDM.setStyleToPageHead(
          `.${themeNamePrefix + item.key} .idm-ant-btn:hover,
          .${themeNamePrefix + item.key} .idm-ant-btn:focus,
          .${themeNamePrefix + item.key} .idm-ant-btn-dashed:hover,
          .${themeNamePrefix + item.key} .idm-ant-btn-dashed:focus`,
          {
            ...cssObject_color_minor,
            ...cssObject_border_minor,
          }
        );
        IDM.setStyleToPageHead(
          `.${themeNamePrefix + item.key} .idm-ant-btn-primary:hover,
          .${themeNamePrefix + item.key} .idm-ant-btn-primary:focus`,
          {
            color: "#FFFFFF",
            ...cssObject_background_minor,
            ...cssObject_border_minor,
          }
        );
      }
    },
    /**
     * 渲染加载指定ID的窗口
     * 首先取出对应的数据，判断是否
     */
    renderDialog(moduleId) {
      const opModule = this.getModuleTree(
        this.PageSettingData.page.layout.children,
        moduleId
      );
      if (!opModule) {
        return false;
      }
      if (opModule.renderType == "firstrender" || opModule.renderType == "activerender") {
        opModule.renderLicence = true;
        if (opModule.renderType == "firstrender" && opModule.loaded) {
          return _.cloneDeep(opModule);
        } else {
          //实时渲染加载之前要把页面组件加载状态设置为false，否则实时渲染的组件消息通信无法准确接收到消息，需要等全部加载完再接收消息
          this.IDM.broadcast.pageModuleLoadCompleteBefore(this.routerId);
          //修改成全部未加载
          this.resetLayoutInnerModuleLoadedStatus(opModule);
          this.initModuleTreeToPage(opModule);
        }
      }
      return _.cloneDeep(opModule);
    },
    setShowDialog(dialogObject) {
      this.dialogObjectList.push(dialogObject);
      $("#router_page_" + this.routerId + " #" + dialogObject.packageid).css(
        "display",
        "flex"
      );
      this.dialogVisibleStatus = true;
      this.dialogIsHideing = false;
    },
    /**
     * 关闭当前打开的窗口
     */
    closeThisOpenDialog(ctx) {
      var customParam = ctx.customParam ? ctx.customParam : ctx;
      var moduleIdArray = [];
      customParam.targetModule.forEach((item) => {
        moduleIdArray.push(item.moduleId);
        this.dialogObjectList.forEach((ditem, dindex) => {
          if (ditem.packageid == item.moduleId) {
            ditem.dialogIsHideing = true;
            $(
              "#router_page_" +
                this.routerId +
                " .idm_dialog_shade_layer_" +
                item.moduleId
            ).attr("hideing", "hideing");
            if (ditem.layerOption) {
              ditem.layerOption.cancel && ditem.layerOption.cancel(ditem);
            }
            //如果是确认按钮调用的关闭则去调用yes方法
            if (ctx.isOkEvent) {
              ditem.layerOption && ditem.layerOption.yes && ditem.layerOption.yes(ditem);
            }
          }
        });
      });
      if (this.dialogObjectList.length <= moduleIdArray.length) {
        this.dialogIsHideing = true;
      }
      let that = this;
      that.sendBroadcastMessage({
        type: "linkageCloseDialog",
        message: ctx,
        rangeModule: moduleIdArray,
      });
      setTimeout(() => {
        customParam.targetModule.forEach((item) => {
          $("#router_page_" + this.routerId + " #" + item.moduleId).css(
            "display",
            "none"
          );
          that.destroyDialog(item.moduleId);
          //从dialogObjectList中删除
          if (that.dialogObjectList.length == 1) {
            that.dialogObjectList = [];
            return;
          }
          let existsIndex = -1;
          that.dialogObjectList.forEach((ditem, dindex) => {
            if (ditem.packageid == item.moduleId) {
              existsIndex = dindex;
              if (ditem.layerOption) {
                ditem.layerOption.end && ditem.layerOption.end(ditem);
              }
            }
          });
          if (existsIndex > -1) {
            //存在就清除
            that.dialogObjectList.splice(existsIndex, 1);
          }
        });
        if (that.dialogObjectList.length == 0) {
          that.dialogVisibleStatus = false;
          that.dialogIsHideing = false;
        }
      }, 500);
      if (ctx.callback) {
        ctx.callback();
      }
    },
    /**
     * 销毁指定ID的窗口
     */
    destroyDialog(moduleId) {
      const opModule = this.getModuleTree(
        this.PageSettingData.page.layout.children,
        moduleId
      );
      if (opModule && opModule.renderType == "activerender") {
        $("#router_page_" + this.routerId + " #" + moduleId).remove();
      }
    },
    /**
     * 显示未加载的隐藏的组件
     * @param {*} moduleIdList
     */
    showHideNoLoadModule(moduleIdList) {
      this.resetLayoutInnerModuleLoadedStatus(
        this.PageSettingData.page.layout.children,
        moduleIdList
      );
      this.initModuleTreeToPage();
    },
    /**
     * 加载组件的自定义函数脚本
     * @param {*} module
     */
    initModulePropCustomBuildCode(module) {
      //追加js脚本
      if (module && module.props) {
        const oldScript = $("#customFun_" + module.packageid);
        var moduleScriptDom = oldScript.length
          ? oldScript
          : $('<script type="text/javascript" id="customFun_' + module.packageid + '"/>');
        let functionList = [];
        this.initModulePropCustomBuildCodeRecursive(module.props, functionList);
        if (functionList.length) {
          var scriptArray = [];
          functionList.forEach((item) => {
            scriptArray.push(`window.${item.name}=${item.customBuildCode}`);
          });
          $("body").append(moduleScriptDom.html(scriptArray.join(";")));
        } else {
          moduleScriptDom.remove();
        }
      }
    },
    initModulePropCustomBuildCodeRecursive(attrObject, functionList) {
      if (IDM.type(attrObject) === "array") {
        //是数组，则查找子级是否包含customBuildCode、customSourceCode、name【IdmAutoGenFun_开头】、idmBuiltin=false的
        for (let index = 0; index < attrObject.length; index++) {
          const item = attrObject[index];
          if (
            item.customBuildCode &&
            item.customSourceCode &&
            item.name &&
            item.idmBuiltin === false &&
            item.name.startsWiths("IdmAutoGenFun_") &&
            IDM.type(item.customBuildCode) === "string" &&
            IDM.type(item.customSourceCode) === "string"
          ) {
            functionList.push(_.cloneDeep(item));
          } else {
            this.initModulePropCustomBuildCodeRecursive(item, functionList);
          }
        }
      } else if (IDM.type(attrObject) === "object") {
        Object.keys(attrObject).forEach((key) => {
          this.initModulePropCustomBuildCodeRecursive(attrObject[key], functionList);
        });
      }
    },

    /**
     * 加载组件的自定义样式代码
     * @param {*} module
     */
    initModulePropCustomBuildStyle(module) {
      //追加js脚本
      if (module && module.props) {
        this.initModulePropCustomBuildStyleRecursive(module.props, module);
      }
    },
    initModulePropCustomBuildStyleRecursive(attrObject, module) {
      if (IDM.type(attrObject) === "array") {
        let paramCom = {
          pageId: this.pageid,
          routerId: this.routerId,
          moduleObject: module,
          ...this.pageInterfaceResultObject,
        };
        //是数组，则查找子级是否包含styleUUID、styleType、cssCode、idmCoreLoad=true的
        for (let index = 0; index < attrObject.length; index++) {
          const item = attrObject[index];
          if (
            item.styleUUID &&
            item.styleType &&
            item.cssCode &&
            item.idmCoreLoad === true &&
            IDM.type(item.cssCode) === "string"
          ) {
            //进行表达式转换
            if (
              !item.styleCondition ||
              (item.styleCondition && IDM.getExpressData(item.styleCondition, paramCom))
            ) {
              let newUUID =
                item.styleUUID + "_" + item.styleRenderUUID
                  ? IDM.getExpressData(item.styleRenderUUID, paramCom, "", false)
                  : IDM.uuid();
              IDM.setStyleHtmlToPageHead(
                newUUID,
                IDM.getExpressData(item.cssCode, paramCom, "", false)
              );
            }
          } else {
            this.initModulePropCustomBuildStyleRecursive(item, module);
          }
        }
      } else if (IDM.type(attrObject) === "object") {
        Object.keys(attrObject).forEach((key) => {
          this.initModulePropCustomBuildStyleRecursive(attrObject[key], module);
        });
      }
    },
    /**
     * 加载组件树到页面中，递归逐级加载
     * moduleData：要渲染的组件数据树结构
     * ischildren：是否为子组件数据
     * fModule：子组件所在的父级容器组件
     * addNextForModulePackageid：要追加到哪个组件的下面（非子级）的packageid
     */
    initModuleTreeToPage(moduleData, ischildren, fModule, addNextForModulePackageid) {
      let that = this;
      moduleData = moduleData || that.PageSettingData.page.layout.children;
      if (!(moduleData instanceof Array)) {
        moduleData = [moduleData];
      }
      //循环取出当前一级未加载的组件
      var loadModule, beforeModule, laterModule;
      moduleData.forEach((item, index) => {
        if (item.comType == "dialog" && item.renderLicence !== true) {
          //查找props
          /*for (let index = 0; index < that.PageSettingData.page.componentsMap.length; index++) {
            const pitem = that.PageSettingData.page.componentsMap[index];
            if (pitem.packageid == item.packageid) {
              item.props = pitem.props;
            }
          }*/
          let dialogRenderType = item.props.compositeAttr.dialogRenderType;
          //beforehand：不用判断直接放行可以进行渲染  firstrender：按需首次渲染  activerender：激活时渲染
          item.renderType = dialogRenderType;
          //renderLicence：渲染许可，true表示可以进行渲染，false表示暂时不渲染跳过
          item.renderLicence = !(
            dialogRenderType == "firstrender" || dialogRenderType == "activerender"
          );
          if (!item.renderLicence) {
            return;
          }
        }
        if (!item.hasLoading && !loadModule) {
          item.hasLoading = true;
          item.loading = true;

          //查找props覆盖树结构中的
          /*
          for (let index = 0; index < that.PageSettingData.page.componentsMap.length; index++) {
            const sitem = that.PageSettingData.page.componentsMap[index];
            if (sitem.packageid == item.packageid) {
              item.props = sitem.props;
            }
          }*/
          loadModule = item;
          //此处要判断一下openAuthorize&&privilege&&role&&dept&&user
          let attrConfig = IDM.setting.applications.basicAttributeAuthorizeConfig;
          let openAuthorize = attrConfig.openAuthorize;
          if (
            (item.props &&
              item.props.basicAttr &&
              item.props.basicAttr.showType == "show") ||
            (item.againLoad === true &&
              item.props &&
              item.props.basicAttr &&
              item.props.basicAttr.showType != "role")
          ) {
            console.log(
              "🚀 ~ file: Page.vue ~ line 1888 ~ moduleData.forEach ~ item.againLoad",
              item.againLoad
            );
            //这里就是直接显示，不做处理
          } else if (
            item.props &&
            item.props.basicAttr &&
            item.props.basicAttr.showType == "hide"
          ) {
            if (item.props.basicAttr.hideType == "load") {
              //加载不显示
              loadModule.loadAndHide = true;
            } else {
              loadModule = null;
            }
            item.loaded = true;
          } else if (
            item.props &&
            item.props.basicAttr &&
            item.props.basicAttr.showType == "custom"
          ) {
            //自定义显示
            let isShowComponent = that.commonCustomEventFunHandle(
              item.props.basicAttr,
              "customShowEvent",
              this.pageInterfaceResultObject
            );
            if (!isShowComponent) {
              loadModule = null;
              item.loaded = true;
            }
          } else if (openAuthorize === true) {
            let privilege = attrConfig.privilege || {},
              privilegePass = -1,
              role = attrConfig.role || {},
              rolePass = -1,
              dept = attrConfig.dept || {},
              deptPass = -1,
              user = attrConfig.user || {},
              userPass = -1;
            //比对权限
            if (
              privilege.open === true &&
              privilege.comparisonObject &&
              item.props &&
              item.props.basicAttr &&
              item.props.basicAttr.privilegeList &&
              item.props.basicAttr.privilegeList.length
            ) {
              let privilegeComparisonObject = IDM.express.replace(
                "@[" + privilege.comparisonObject + "]",
                { IDM: IDM, window, ...this.pageInterfaceResultObject }
              );
              if (privilege.separator && IDM.type(privilegeComparisonObject) != "array") {
                privilegeComparisonObject = privilegeComparisonObject.split(
                  privilege.separator
                );
              }
              //选择了还没比对要设置为false不通过，下面有交集了再设置为true通过
              privilegePass = false;
              //直接用选择的权限去判断
              item.props.basicAttr.privilegeList.forEach((iitem) => {
                let comparisonKey = IDM.express.replace(
                  "@[" + (privilege.comparisonKey || "key") + "]",
                  iitem
                );
                if (
                  privilegeComparisonObject === undefined ||
                  privilegeComparisonObject.indexOf(comparisonKey) > -1
                ) {
                  privilegePass = true;
                }
              });
            }
            //比对角色
            if (
              role.open === true &&
              role.comparisonObject &&
              item.props &&
              item.props.basicAttr &&
              item.props.basicAttr.roleList &&
              item.props.basicAttr.roleList.length
            ) {
              let roleComparisonObject = IDM.express.replace(
                "@[" + role.comparisonObject + "]",
                { IDM: IDM, window, ...this.pageInterfaceResultObject }
              );
              if (role.separator && IDM.type(roleComparisonObject) != "array") {
                roleComparisonObject = roleComparisonObject.split(role.separator);
              }
              //选择了还没比对要设置为false不通过，下面有交集了再设置为true通过
              rolePass = false;
              //直接用选择的权限去判断
              item.props.basicAttr.roleList.forEach((iitem) => {
                let comparisonKey = IDM.express.replace(
                  "@[" + (role.comparisonKey || "key") + "]",
                  iitem
                );
                if (
                  roleComparisonObject === undefined ||
                  roleComparisonObject.indexOf(comparisonKey) > -1
                ) {
                  rolePass = true;
                }
              });
            }
            //比对部门
            if (
              dept.open === true &&
              dept.comparisonObject &&
              item.props &&
              item.props.basicAttr &&
              item.props.basicAttr.deptList &&
              item.props.basicAttr.deptList.length
            ) {
              let deptComparisonObject = IDM.express.replace(
                "@[" + dept.comparisonObject + "]",
                { IDM: IDM, window, ...this.pageInterfaceResultObject }
              );
              if (dept.separator && IDM.type(deptComparisonObject) != "array") {
                deptComparisonObject = deptComparisonObject.split(dept.separator);
              }
              //选择了还没比对要设置为false不通过，下面有交集了再设置为true通过
              deptPass = false;
              //直接用选择的权限去判断
              item.props.basicAttr.deptList.forEach((iitem) => {
                let comparisonKey = IDM.express.replace(
                  "@[" + (dept.comparisonKey || "key") + "]",
                  iitem
                );
                if (
                  deptComparisonObject === undefined ||
                  deptComparisonObject.indexOf(comparisonKey) > -1
                ) {
                  deptPass = true;
                }
              });
            }
            //对比部门集合（包含贯穿下级的）
            if (
              dept.open === true &&
              dept.comparisonObject &&
              item.props &&
              item.props.basicAttr &&
              item.props.basicAttr.deptColumns &&
              item.props.basicAttr.deptColumns.length
            ) {
              let deptComparisonObject = IDM.express.replace(
                "@[" + dept.comparisonObject + "]",
                { IDM: IDM, window, ...this.pageInterfaceResultObject }
              );
              if (dept.separator && IDM.type(deptComparisonObject) != "array") {
                deptComparisonObject = deptComparisonObject.split(dept.separator);
              }
              //选择了还没比对要设置为false不通过，下面有交集了再设置为true通过
              deptPass = false;
              if (deptComparisonObject === undefined) {
                deptPass = true;
              } else {
                //直接用选择的权限去判断
                item.props.basicAttr.deptColumns.forEach((iitem) => {
                  //对比，如果不包含下级则只对比最后一个，如果包含下级则只要对比出来了则算通过
                  let deptList = iitem.deptList,
                    includeChild = iitem.includeChild;
                  deptList &&
                    deptList.forEach((deptItem) => {
                      let comparisonKey = IDM.express.replace(
                        "@[" + (dept.comparisonKey || "key") + "]",
                        deptItem
                      );
                      if (
                        (includeChild == "0" &&
                          IDM.type(deptComparisonObject) == "array" &&
                          deptComparisonObject[deptComparisonObject.length - 1] ==
                            comparisonKey) ||
                        (includeChild == "1" &&
                          deptComparisonObject.indexOf(comparisonKey) > -1)
                      ) {
                        deptPass = true;
                      }
                    });
                });
              }
            }
            //比对用户
            if (
              user.open === true &&
              user.comparisonObject &&
              item.props &&
              item.props.basicAttr &&
              item.props.basicAttr.userList &&
              item.props.basicAttr.userList.length
            ) {
              let userComparisonObject = IDM.express.replace(
                "@[" + user.comparisonObject + "]",
                { IDM: IDM, window, ...this.pageInterfaceResultObject }
              );
              //选择了还没比对要设置为false不通过，下面有交集了再设置为true通过
              userPass = false;
              //直接用选择的权限去判断
              let userArray = item.props.basicAttr.userList.map((item) => {
                let comparisonKey = IDM.express.replace(
                  "@[" + (user.comparisonKey || "key") + "]",
                  item
                );
                return comparisonKey;
              });
              if (userArray.indexOf(userComparisonObject) > -1) {
                userPass = true;
              }
            }
            //如果不为空则需要判断是否通过
            if (
              !(
                (privilegePass === -1 &&
                  rolePass === -1 &&
                  deptPass === -1 &&
                  userPass === -1) ||
                (privilegePass !== -1 && privilegePass) ||
                (rolePass !== -1 && rolePass) ||
                (deptPass !== -1 && deptPass) ||
                (userPass !== -1 && userPass)
              )
            ) {
              loadModule = null;
              item.loaded = true;
            }
          }
          if (loadModule) {
            //如果不为空，则取出下一个非弹窗组件
            for (let i = 0; i < moduleData.length; i++) {
              if (moduleData[i].comType != "dialog" && index > i) {
                beforeModule = moduleData[i];
              } else if (index < i && moduleData[i].comType != "dialog" && !laterModule) {
                laterModule = moduleData[i];
              }
              if (beforeModule && laterModule) {
                break;
              }
            }
          }
        }
      });
      if (!loadModule) {
        //一级没有待加载的，再去加载子级
        moduleData.forEach((item) => {
          if (item.comType == "dialog" && !item.renderLicence) {
            //渲染许可为false跳过不渲染下面的子级
            return;
          }
          if (item.loaded && item.children && item.children.length > 0) {
            that.initModuleTreeToPage(item.children, true, item);
          }
        });
        //判断是否加载完成，如果加载完成需要去调用统一接口并设置页面加载已完成，否则组件通信用不了
        if (loadModule == null && this.pageModuleDataIsLoaded(null, true, "loaded")) {
          this.requestPageCommonInterfaceData(true);
        }
      } else {
        this.initModulePropCustomBuildCode(loadModule);
        //加载组件样式，样式内部支持使用页面接口返回的数据
        this.initModulePropCustomBuildStyle(loadModule);
        //追加容器
        let comObject = loadModule;
        comObject.mountComplete = this.moduleMountComplete;
        comObject.moduleReload = this.moduleReload;
        comObject.dynamicRenderModuleGroupInitData = this.dynamicRenderModuleGroupInitData;
        comObject.removeDynamicRenderModuleGroup = this.removeDynamicRenderModuleGroup;
        comObject.getModuleChildrenPackageId = this.getModuleChildrenPackageId;
        //追加div
        let newComDiv = that.createModuleDom(comObject);
        let containerId = "";
        if (comObject.comType == "dialog") {
          containerId = that.PageSettingData.id;
          $("#idm_dialog_wrap_" + that.routerId).append(newComDiv);
        } else if (
