          if (beforeModule) {
            $("#router_page_" + that.routerId + " #" + beforeModule.packageid).after(
              newComDiv
            );
          } else if (laterModule) {
            $("#router_page_" + that.routerId + " #" + laterModule.packageid).before(
              newComDiv
            );
          }
        }
        //这里要做渲染，根据relationId: "component_o0mOWZACB0IVR5hz"   relationType: "top"
        //关联ID
        comObject.env = "production";
        //追加路由ID，页面ID
        comObject.routerId = this.routerId;
