    },
    /**
     * 判断是否加载完成,未完成返回false，主要用于拷贝的时候递归加载完成的判断
     * loadType:加载类型：loaded 是否全部加载完成、loading：加载中的、hasLoading：已经开始加载的
     * waitRemoveModuleId：等待删除已加载的组件ID数组
     */
    pageModuleDataIsLoaded(
      moduleData,
      isnotloadinner,
      loadType = "hasLoading",
      waitRemoveModuleId
    ) {
      let that = this;
      var isLoaded = true;
      moduleData = moduleData || that.PageSettingData.page.layout.children;
      if (!(moduleData instanceof Array)) {
        moduleData = [moduleData];
      }
      moduleData.forEach((item) => {
        if (item.comType == "dialog" && !item.renderLicence) {
          //渲染许可为false跳过不渲染下面的子级
          return;
        }
        //无载体的组件也要设置为已加载完成
        if (!item[loadType] && item.nocontainer !== true) {
          isLoaded = false;
          // if(isnotloadinner&&(item.containerIndex||item.containerIndex=="0")){
          //   isLoaded = true;
          // }
        }
        if (item[loadType]) {
          //已经渲染的，可移除
          waitRemoveModuleId &&
            _.remove(waitRemoveModuleId, function (n) {
              return n == item.packageid;
            });
        }
      });
      if (isLoaded) {
        moduleData.forEach((item) => {
          if (item.comType == "dialog" && !item.renderLicence) {
            //渲染许可为false跳过不渲染下面的子级
            return;
          }
          if (item.children && item.children.length > 0 && isLoaded) {
            isLoaded = this.pageModuleDataIsLoaded(
              item.children,
              isnotloadinner,
              loadType,
              waitRemoveModuleId
            );
          }
        });
      }
      return isLoaded;
    },
    /**
     * 创建组件dom外部元素
