      comObject.packageid = comObject.packageid || "component_" + IDM.UUID();
      //组件内部ID，这里可能不受核心框架控制，需要谨慎使用
      comObject.id = comObject.id || comObject.packageid + "_inner";
      //追加div
      var newComDiv = $("<div />");
      //追加设置属性
      newComDiv.attr("id", comObject.packageid);
