      newComDiv.addClass("idm_module_absolute");
      this.resetHoverModuleStyleInfo(newComDiv, comObject);
      //这里可以继续处理内置的组件事件，比如svg绘制的
    },
    /**
     * 重绘样式
     * @param {*} moduleDOM
     * @param {*} currentModule
     */
    resetHoverModuleStyleInfo(moduleDOM, currentModule) {
      let that = this;
      let _basicAttr =
        currentModule.props && currentModule.props.basicAttr
          ? currentModule.props && currentModule.props.basicAttr
          : {};
      //获取方位、坐标、宽高
      const modulePositionOrigin = _basicAttr.positionOrigin || "leftTop";
      let moduleCurrentWH =
        _basicAttr.positionWH ||
        `${that.defaultModuleSize.width}.${that.defaultModuleSize.height}`;
      moduleCurrentWH = moduleCurrentWH.split(",");
      let moduleCurrentW =
          moduleCurrentWH.length > 0 ? parseFloat(moduleCurrentWH[0]) : 0,
        moduleCurrentH = moduleCurrentWH.length > 1 ? parseFloat(moduleCurrentWH[1]) : 0;
      let moduleCurrentXY = _basicAttr.positionXY || "0,0";
      moduleCurrentXY = moduleCurrentXY.split(",");
      let moduleCurrentX =
          moduleCurrentXY.length > 0 ? parseFloat(moduleCurrentXY[0]) : 0,
        moduleCurrentY = moduleCurrentXY.length > 1 ? parseFloat(moduleCurrentXY[1]) : 0;

      //获取索引
      let moduleZindex = _basicAttr.zIndex || 0;
      let moduleOpacity = _basicAttr.opacity == 0 ? 0 : _basicAttr.opacity || 100;
      let moduleScale = _basicAttr.scale || 1;
      let moduleRotateX = _basicAttr.rotateX || 0;
      let moduleRotateY = _basicAttr.rotateY || 0;
      let moduleRotateZ = _basicAttr.rotateZ || 0;

      let styleObject = {};
      switch (modulePositionOrigin) {
        case "leftTop":
          styleObject = {
            left: moduleCurrentX + "px",
            top: moduleCurrentY + "px",
            right: "auto",
            bottom: "auto",
            width: moduleCurrentW + "px",
            height: moduleCurrentH + "px",
          };
          break;
        case "rightTop":
          styleObject = {
            left: "auto",
            top: moduleCurrentY + "px",
            right: moduleCurrentX + "px",
            bottom: "auto",
            width: moduleCurrentW + "px",
            height: moduleCurrentH + "px",
          };
          break;
        case "rightBottom":
          styleObject = {
            left: "auto",
            top: "auto",
            right: moduleCurrentX + "px",
            bottom: moduleCurrentY + "px",
            width: moduleCurrentW + "px",
            height: moduleCurrentH + "px",
          };
          break;
        case "leftBottom":
          styleObject = {
            left: moduleCurrentX + "px",
            top: "auto",
            right: "auto",
            bottom: moduleCurrentY + "px",
            width: moduleCurrentW + "px",
            height: moduleCurrentH + "px",
          };
          break;
      }
      if (_basicAttr.positionType == "fixed") {
        styleObject["position"] = "fixed";
      }
      styleObject["z-index"] = moduleZindex;
      styleObject["opacity"] = moduleOpacity / 100;
      styleObject[
        "transform"
      ] = `scale(${moduleScale}) rotateX(${moduleRotateX}deg) rotateY(${moduleRotateY}deg) rotateZ(${moduleRotateZ}deg)`;
      moduleDOM && moduleDOM.css(styleObject);
    },
    /**
     * 获取指定层级的组件树结构数据
     */
    getModuleTree(module, packageid) {
      if (!(module instanceof Array)) {
        module = [module];
      }
      var _findModule;
      module.forEach((item) => {
        if (item.packageid == packageid) {
          _findModule = item;
        } else if (!_findModule && item.children) {
          _findModule = this.getModuleTree(item.children, packageid);
        }
      });
      return _findModule;
    },
    /**
     * 获取组件的子组件packageid集合，返回数组
     * @param {*} packageid 父组件packageid
     * @param {*} idmContainerId 要获取所在容器的containerIndex，如果为空则获取所有的
     * @param {*} containerGroup 是否容器分组，如果是则containerIndex需要用 idmContainerId+"_" 前缀这种方式查找
     */
    getModuleChildrenPackageId(packageid, idmContainerId, containerGroup) {
      let allChildrenPackageIdArray = [];
      const filterModuleTree = this.getModuleTree(
        this.PageSettingData.page.layout.children,
        packageid
      );
      if (!filterModuleTree || filterModuleTree?.children?.length == 0) {
        return allChildrenPackageIdArray;
      }
      let existsRenderModule = filterModuleTree.children.filter(
        (item) =>
          item.containerIndex == idmContainerId ||
          (containerGroup && item.containerIndex.startsWiths(idmContainerId + "_"))
      );

      this.findTreeAllModulePackageId(existsRenderModule, allChildrenPackageIdArray);

      return allChildrenPackageIdArray;
    },
    /**
     * 动态渲染复制组件组的组件且加载数据，每次只复制一个分组(只有不包含的dynamicModule)
     * 动态渲染需要移除之前的重新渲染一遍。
     * @param {*} packageid 组件packageid，要动态渲染复制的父级组件
     * @param {*} idmContainerId 要动态渲染的容器索引ID
     * @param {*} itemData 动态渲染组件所使用到的数据
     * @param {*} containerGroup 是否容器分组，如果是则containerIndex需要用 idmContainerId+"_" 前缀这种方式查找
     * @param {*} sendMessageKey 发送联动消息的messageKey标识，为空则默认为dynamicRenderModule
     */
    dynamicRenderModuleGroupInitData(
      packageid,
      idmContainerId,
      itemData,
      containerGroup,
      sendMessageKey
    ) {
      debugger;
      //根据packageid找出该组件下的所有组件进行复制（支持树结构式组件），复制完成后然后把数据以组件通信的消息方式发送给对应复制后的组件
      const copyModuleTree = this.getModuleTree(
        this.PageSettingData.page.layout.children,
        packageid
      );
      if (
        !copyModuleTree ||
        (copyModuleTree && !copyModuleTree.children) ||
        (copyModuleTree && copyModuleTree.children && copyModuleTree.children.length == 0)
      ) {
        return;
      }
      //先查找idmContainerId是否存在
      let existsRenderModule = copyModuleTree.children.filter(
        (item) =>
          item.containerIndex == idmContainerId ||
          (containerGroup && item.containerIndex.startsWiths(idmContainerId + "_"))
      );

      if (existsRenderModule && existsRenderModule.length) {
        //只移除dom，不移除数据
        existsRenderModule.forEach((item) => {
          $("#router_page_" + this.routerId + " #" + item.packageid).remove();
        });
        let allModulePackageId = [];
        this.findTreeAllModulePackageId(existsRenderModule, allModulePackageId, true);
        //把当前的参数存储到待消费变量中，等待组件加载的时候去判断
        this.dynamicWaitExpendObject.push({
          packageid,
          allModulePackageId,
          idmContainerId,
          itemData,
          sendMessageKey,
        });
        //删除全部加载状态
        this.resetLayoutInnerModuleLoadedStatus(existsRenderModule);

        //调用重新加载
        this.initModuleTreeToPage(existsRenderModule, true, copyModuleTree, false);
        return;
      }

      let copyModuleArray = _.cloneDeep(
        copyModuleTree.children.filter((item) => !item.dynamicModule)
      );
      //发现如果没有则随机取一批复制
      if (!copyModuleArray || (copyModuleArray && copyModuleArray.length == 0)) {
        let firstContainerId = copyModuleTree.children[0].containerIndex;
        if (containerGroup) {
          let oiArray = firstContainerId.split("_");
          if (oiArray.length > 1) {
            oiArray.splice(oiArray.length - 1, 1);
          }
          firstContainerId = oiArray.join("_");
        }
        copyModuleArray = _.cloneDeep(
          copyModuleTree.children.filter(
            (item) =>
              item.containerIndex == firstContainerId ||
              (containerGroup && item.containerIndex.startsWiths(firstContainerId + "_"))
          )
        );
      }

      //更换containerIndex
      copyModuleArray.forEach((item) => {
        const ciArray = item.containerIndex.split("_");
        if (containerGroup && ciArray.length > 1) {
          item.containerIndex = idmContainerId + "_" + ciArray[ciArray.length - 1];
        } else {
          item.containerIndex = idmContainerId;
        }
      });

      let allModulePackageId = [];
      //重新刷新所有的id并且赋值dynamicModule=true且从map中拷贝一份
      this.refreshTreeModulePrimaryKey(
        copyModuleArray,
        allModulePackageId,
        idmContainerId,
        containerGroup
      );
      this.refreshModuleAndMapGlobalId(copyModuleArray, copyModuleTree);

      //把当前的参数存储到待消费变量中，等待组件加载的时候去判断
      this.dynamicWaitExpendObject.push({
        packageid,
        allModulePackageId,
        idmContainerId,
        itemData,
        sendMessageKey,
      });

      //合并数组
      copyModuleTree.children.push.apply(copyModuleTree.children, copyModuleArray);

      //删除全部加载状态
      this.resetLayoutInnerModuleLoadedStatus(copyModuleArray);

      //调用重新加载
      this.initModuleTreeToPage(copyModuleArray, true, copyModuleTree, false);
      console.log(this.PageSettingData);
    },
    /**
     * 移除动态渲染的组件组
     * 如果删除的是原始组件需要找其中一条修改为原始组件，如果是只有一个索引了则不移除数据只移除dom
     * @param {*} packageid  组件packageid，动态渲染复制的父级组件
     * @param {*} idmContainerId 动态渲染的容器索引ID
     * @param {*} containerGroup 是否容器分组，如果是则containerIndex需要用 idmContainerId+"_" 前缀这种方式查找
     */
    removeDynamicRenderModuleGroup(packageid, idmContainerId, containerGroup) {
      const copyModuleTree = this.getModuleTree(
        this.PageSettingData.page.layout.children,
        packageid
      );
      //查找containerIndex的数量(不包含原始)和原始的containerIndex
      let containerIndexArray = [],
        originalIndex = "";
      copyModuleTree.children &&
        copyModuleTree.children.forEach((item) => {
          let newContainerIndex = item.containerIndex;
          if (containerGroup) {
            let oiArray = newContainerIndex.split("_");
            if (oiArray.length > 1) {
              oiArray.splice(oiArray.length - 1, 1);
            }
            newContainerIndex = oiArray.join("_");
          }
          if (!originalIndex && !item.dynamicModule) {
            originalIndex = newContainerIndex;
          }
          if (!containerIndexArray.includes(newContainerIndex) && item.dynamicModule) {
            containerIndexArray.push(newContainerIndex);
          }
        });
      if (originalIndex == idmContainerId && containerIndexArray.length) {
        //更换初始
        const newOriginalIndex = containerIndexArray[0];
        copyModuleTree.children.forEach((item) => {
          if (
            item.containerIndex == newOriginalIndex ||
            (containerGroup && item.containerIndex.startsWiths(newOriginalIndex + "_"))
          ) {
            item.dynamicModule = false;
          }
        });
      }
      if (originalIndex) {
        containerIndexArray.push(originalIndex);
      }
      if (containerIndexArray.length == 1) {
        //只移除dom，不移除数据
        copyModuleTree.children.forEach((item) => {
          $("#router_page_" + this.routerId + " #" + item.packageid).remove();
        });
        return;
      }
      const removeAllPackageId = this.getModuleChildrenPackageId(
        packageid,
        idmContainerId,
        containerGroup
      );
      this.deletedDynamicModuleMap(removeAllPackageId);
      //删除树结构的
      copyModuleTree.children &&
        _.remove(copyModuleTree.children, function (n) {
          return (
            n.containerIndex == idmContainerId ||
            (containerGroup && n.containerIndex.startsWiths(idmContainerId + "_"))
          );
        });
    },
    /**
     * 递归删除map数据
     * @param {*} packageid 要删除的组件或者父级的组件
     * @param {*} ischildren 是否删除子组件
     * @param {*} containerIndex 要删除的容器下的直属索引，如果跨结构也直接删除
     */
    deletedDynamicModuleMap(removeAllPackageId) {
      var isdel = false;
      this.PageSettingData.page.componentsMap.forEach((item, index) => {
        if (removeAllPackageId.includes(item.packageid)) {
          isdel = true;
          this.PageSettingData.page.componentsMap.splice(index, 1);
          $("#router_page_" + this.routerId + " #" + item.packageid).remove();
        }
      });
      if (isdel) {
        this.deletedDynamicModuleMap(removeAllPackageId);
      }
    },
    /**
     * 查找汇总所有的组件包id
     * @param {*} moduleData 要查找的树结构
     * @param {*} allModulePackageId 存放所有的组件包ID变量
     * @param {*} setDynamicModule 是否设置组件属性dynamicModuleMessage = true;
     */
    findTreeAllModulePackageId(moduleData, allModulePackageId, setDynamicModule) {
      if (!(moduleData instanceof Array)) {
        moduleData = [moduleData];
      }
      moduleData.forEach((item) => {
        allModulePackageId.push(item.packageid);
        if (setDynamicModule) {
          item.dynamicModuleMessage = true;
        }
        item.children &&
          this.findTreeAllModulePackageId(
            item.children,
            allModulePackageId,
            setDynamicModule
          );
      });
    },
    /**
     * 刷新组件的主键以及赋值属性为动态渲染组件
     * @param {*} moduleData
     */
    refreshTreeModulePrimaryKey(
      moduleData,
      allModulePackageId,
      idmContainerId,
      containerGroup
    ) {
      if (!(moduleData instanceof Array)) {
        moduleData = [moduleData];
      }
      moduleData.forEach((item) => {
        //查找到map中的并拷贝一份修改id和packageid然后追加到map中
        let currentMapData = this.PageSettingData.page.componentsMap.filter(
          (sitem) => sitem.packageid == item.packageid
        );
        const modulePackageid = "component_" + IDM.uuid();
        item.packageid = modulePackageid;
        item.id = modulePackageid + "_inner";
        item.dynamicModule = true;
        let newCurrentMapData =
          currentMapData && currentMapData.length ? _.cloneDeep(currentMapData[0]) : {};
        newCurrentMapData.packageid = modulePackageid;
        newCurrentMapData.id = modulePackageid + "_inner";
        newCurrentMapData.dynamicModule = true;

        if (idmContainerId) {
          const ciArray = newCurrentMapData.containerIndex.split("_");
          if (containerGroup && ciArray.length > 1) {
            newCurrentMapData.containerIndex =
              idmContainerId + "_" + ciArray[ciArray.length - 1];
          } else {
            newCurrentMapData.containerIndex = idmContainerId;
          }
        }
        this.PageSettingData.page.componentsMap.push(newCurrentMapData);
        allModulePackageId.push(modulePackageid);
        item.children &&
          this.refreshTreeModulePrimaryKey(item.children, allModulePackageId);
      });
    },
    /**
     * 刷新组件树结构和map的globalid
     * @param {*} module 要刷新的树结构
     * @param {*} fmodule
     */
    refreshModuleAndMapGlobalId(module, fmodule) {
      if (!(module instanceof Array)) {
        module = [module];
      }
      var _findModule;
      module.forEach((item) => {
        item.globalId = fmodule.globalId + "." + item.packageid;
        //对map循环
        this.PageSettingData.page.componentsMap.forEach((sitem) => {
          if (item.packageid == sitem.packageid) {
            sitem.globalId = item.globalId;
          }
        });
        if (item.children) {
          this.refreshModuleAndMapGlobalId(item.children, item);
        }
      });
      return _findModule;
    },
    /**
     * 提供组件或组件内部容器的重新加载
     */
    moduleReload(packageid, innerContainerIndex) {
      //先查找模块，然后根据innerContainerIndex来删除对应的加载状态标识
      const reloadModule = this.getModuleTree(
        this.PageSettingData.page.layout.children,
        packageid
      );
      let reloadModuleArray = [reloadModule];
      let parentModule;
      if (innerContainerIndex || innerContainerIndex == "0") {
        //存在内部索引
        reloadModuleArray = [];
        reloadModule &&
          reloadModule.children &&
          reloadModule.children.forEach((item) => {
            if (item.containerIndex == innerContainerIndex) {
              reloadModuleArray.push(item);
            }
          });
        parentModule = reloadModule;
      } else {
        //找出当前的父级
        let globalIdArray = reloadModule.globalId.split(".");
        const globalId =
          globalIdArray.length <= 2
            ? globalIdArray[0]
            : globalIdArray[globalIdArray.length - 2];
        parentModule = this.getModuleTree(
          this.PageSettingData.page.layout.children,
          globalId
        );
      }
      //删除全部加载状态
      this.resetLayoutInnerModuleLoadedStatus(reloadModuleArray);
      reloadModuleArray &&
        reloadModuleArray.forEach((item) => {
          $("#router_page_" + this.routerId + " #" + item.packageid).remove();
        });

      //调用重新加载
      this.initModuleTreeToPage(reloadModuleArray, true, parentModule, false);
    },
    /**
     * 组件挂载完成回调事件
     */
    moduleMountComplete(module) {
      let that = this;
      //把事件合并到map中
      this.PageSettingData.page.componentsMap.forEach((item) => {
        if (item.packageid == module.packageid) {
          item.idmBroadcastMessage = module.idmBroadcastMessage;
          item.idmSetContextValue = module.idmSetContextValue;
          item.idmGetContextValue = module.idmGetContextValue;
        }
      });
      console.log("挂载完成，回调返回的数据", module);
      let opModule = this.getModuleTree(
        this.PageSettingData.page.layout.children,
        module.packageid
      );
      //加载完成，不在加载中了
      opModule.loading = false;
      //设置加载完成的状态
      opModule.loaded = true;
      if (opModule.children && opModule.children.length > 0) {
        //有子组件，代表是容器类组件，首先获取是否加载完成且有子元素inner
        const targetNode = $("#" + module.packageid);
        if (targetNode.find(":visible").length == 0) {
          //代表内容元素还是空的，没有加载完成
          var invcount = 0;
          var intval = setInterval(function () {
            invcount++;
            if (
              $("#router_page_" + that.routerId + " #" + module.packageid).find(
                ":visible"
              ).length == 0 &&
              invcount <= 20
            ) {
              return;
            }
            clearInterval(intval);
            that.moduleMountCompleteCommon(module, opModule);
          }, 5);
        } else {
          //加载时序问题，延迟3毫秒
          setTimeout(() => {
            that.moduleMountCompleteCommon(module, opModule);
          }, 3);
        }
      } else {
        //加载时序问题，延迟3毫秒
        setTimeout(() => {
          that.moduleMountCompleteCommon(module, opModule);
        }, 3);
      }
      // this.initModuleInnerEventHandle(module);
      // class名称表示：
      // drag_container_empty：空容器类名
      // drag_container：容器，可拖拽过来
      // id作为class类名：样式设置
      // idm_module：所有组件最外层都有的类名
      // drag_container_inlineblock: 代表是行内容器

      // 属性 ：
      // idm-com-anchor-id：代表新生成的唯一组件ID
      // id:代表新生成的唯一组件ID
      // idm-com-id：组件的ID
      // idm-com-classid：组件的包名
      // idm-com-classname：组件的类名
      // idm-container-index：容器索引，在一个组件内有两个或以上的容器就会作用到
    },
    moduleMountCompleteCommon(module, opModule) {
      let that = this;
      var moduleDom = $("#router_page_" + this.routerId + " #" + module.packageid + "");
      var moduleInnerDom = moduleDom.find(">*[idm-ctrl]");
      var idmCtrl = moduleInnerDom.attr("idm-ctrl"),
        idmCtrlId = moduleInnerDom.attr("idm-ctrl-id"),
        domId = moduleInnerDom.attr("id");
      if (!domId) {
        moduleInnerDom.attr("id", idmCtrlId);
      }
      switch (idmCtrl) {
        case "drag_container": //容器
          moduleDom.addClass("drag_container");
          break;
        case "drag_container_inlineblock": //行内容器
          moduleDom.addClass("drag_container drag_container_inlineblock");
          break;
        case "idm_module": //非容器组件
          break;
      }

      //如果没有加载完成则先判断下，没有完成则继续加载
      if (!this.pageModuleDataIsLoaded(null, true)) {
        this.$nextTick(function () {
          this.initModuleTreeToPage(null, true, this.PageSettingData.page.layout);
        });
      } else if (this.pageModuleDataIsLoaded(null, true, "loaded")) {
        this.requestPageCommonInterfaceData(true);
      }
      //这里需要判断动态渲染是否已经消费完成 this.dynamicWaitExpendObject
      if (opModule.dynamicModule || opModule.dynamicModuleMessage) {
        //进行与 dynamicWaitExpendObject 获取判断
        let globalIdArray = opModule.globalId.split(".");
        const parentModuleId =
          globalIdArray[globalIdArray.length - 2] || globalIdArray[0];
        this.dynamicWaitExpendObject.forEach((ditem, index) => {
          if (
            globalIdArray.includes(ditem.packageid) &&
            ditem.allModulePackageId.includes(opModule.packageid)
          ) {
            let parentModuleTree = this.getModuleTree(
              this.PageSettingData.page.layout.children,
              ditem.packageid
            );
            //用ditem.allModulePackageId去判断是否已经全部加载完毕，如果全部加载完毕则需要给这些组件发送消息
            let allModulePackageId = _.cloneDeep(ditem.allModulePackageId);
            this.pageModuleDataIsLoaded(
              parentModuleTree.children,
              true,
              "loaded",
              allModulePackageId
            );
            if (allModulePackageId.length == 0) {
              //已经全部加载完毕，可以发送联动结果消息了 linkageResult
              IDM.broadcast.send({
                type: "linkageResult",
                message: ditem.itemData,
                rangeModule: ditem.allModulePackageId,
                messageKey: ditem.sendMessageKey || "dynamicRenderModule",
              });
              this.dynamicWaitExpendObject.splice(index, 1);
            }
          }
        });
      }
    },
    /**
     * 加载组件内部容器组件事件
     * 参数：
     * module：{packageid:组件包id，组件内部使用的id}
     */
    initModuleInnerEventHandle(module) {
      let that = this;
      //获取是否存在内部容器组件
      var moduleDom = $("#router_page_" + that.routerId + " #" + module.packageid + "");
      var moduleInnerDom = moduleDom.find(
        ".drag_container[idm-ctrl-id='" + module.id + "'][idm-ctrl-inner]"
      );
      if (moduleInnerDom.length > 0) {
        //内部组件重新追加，因为上面哪种加载方式会导致没加载完
        const opModule = this.getModuleTree(
          this.PageSettingData.page.layout.children,
          module.packageid
        );
        this.resetLayoutInnerModuleLoadedStatus(opModule.children);
        if (opModule.children && opModule.children.length > 0) {
          opModule.children.forEach((item) => {
            $("#" + item.packageid).remove();
          });
          this.initModuleTreeToPage(opModule.children, true, opModule);
        }
      }
    },
    /**
     * 加载前重置所有加载状态
     * @param {*} module
     * @param {*} resetModuleIdList
     */
    resetLayoutInnerModuleLoadedStatus(module, resetModuleIdList) {
      if (!(module instanceof Array)) {
        module = [module];
      }
      module.forEach((item) => {
        let hasFindLoadId =
          resetModuleIdList && resetModuleIdList.indexOf(item.packageid) > -1;
        let childrenResetModuleIdList = resetModuleIdList;
        if (!resetModuleIdList || hasFindLoadId) {
          //组件是否已经加载完成
          delete item.loaded;
          //组件是否在加载中...
          delete item.loading;
          //组件是否已经加载
          delete item.hasLoading;
          //组件的无载体容器属性
          delete item.nocontainer;
          if (hasFindLoadId) {
            item.againLoad = true;
            childrenResetModuleIdList = null;
          }
        }
        if (item.children && item.children.length > 0) {
          this.resetLayoutInnerModuleLoadedStatus(
            item.children,
            childrenResetModuleIdList
          );
        }
      });
    },

    addModuleEndpoint(module) {
      //判断是否需要加载连线
      let propData = this.PageSettingData.page.layout.props.compositeAttr || {};
      if (propData.openJsPlumb !== true) {
        return;
      }
      var common = {
        isSource: true,
        isTarget: true,
        endpoint: "Dot",
      };
      let _basicAttr =
        module.props && module.props.basicAttr
          ? module.props && module.props.basicAttr
          : {};
      let offsetsTop = [0, 0],
        offsetsTopRight = [0, 0],
        offsetsRight = [0, 0],
        offsetsBottomRight = [0, 0],
        offsetsBottom = [0, 0],
        offsetsBottomLeft = [0, 0],
        offsetsLeft = [0, 0],
        offsetsTopLeft = [0, 0];
      if (_basicAttr.AnchorsTopLeft) {
        offsetsTop = _basicAttr.AnchorsTop.split(",");
        offsetsTopRight = _basicAttr.AnchorsTopRight.split(",");
        offsetsRight = _basicAttr.AnchorsRight.split(",");
        offsetsBottomRight = _basicAttr.AnchorsBottomRight.split(",");
        offsetsBottom = _basicAttr.AnchorsBottom.split(",");
        offsetsBottomLeft = _basicAttr.AnchorsBottomLeft.split(",");
        offsetsLeft = _basicAttr.AnchorsLeft.split(",");
        offsetsTopLeft = _basicAttr.AnchorsTopLeft.split(",");
      }
      debugger;
      //基本属性里面直接填写8个点的偏移值
      let endpointList = [
        {
          anchor: [0.5, 0, 0, -1, parseFloat(offsetsTop[0]), parseFloat(offsetsTop[1])],
          cssClass: module.packageid + "_endpoint idm_flow_endpoint " + "endpoint-top",
        },
        {
          anchor: [
            1,
            0,
            0,
            -1,
            parseFloat(offsetsTopRight[0]),
            parseFloat(offsetsTopRight[1]),
          ],
          cssClass:
            module.packageid + "_endpoint idm_flow_endpoint " + "endpoint-top-right",
        },
        {
          anchor: [
            1,
            0.5,
            1,
            0,
            parseFloat(offsetsRight[0]),
            parseFloat(offsetsRight[1]),
          ],
          cssClass: module.packageid + "_endpoint idm_flow_endpoint " + "endpoint-right",
        },
        {
          anchor: [
            1,
            1,
            0,
            1,
            parseFloat(offsetsBottomRight[0]),
            parseFloat(offsetsBottomRight[1]),
          ],
          cssClass:
            module.packageid + "_endpoint idm_flow_endpoint " + "endpoint-bottom-right",
        },
        {
          anchor: [
            0.5,
            1,
            0,
            1,
            parseFloat(offsetsBottom[0]),
            parseFloat(offsetsBottom[1]),
          ],
          cssClass: module.packageid + "_endpoint idm_flow_endpoint " + "endpoint-bottom",
        },
        {
          anchor: [
            0,
            1,
            0,
            1,
            parseFloat(offsetsBottomLeft[0]),
            parseFloat(offsetsBottomLeft[1]),
          ],
          cssClass:
            module.packageid + "_endpoint idm_flow_endpoint " + "endpoint-bottom-left",
        },
        {
          anchor: [0, 0.5, -1, 0, parseFloat(offsetsLeft[0]), parseFloat(offsetsLeft[1])],
          cssClass: module.packageid + "_endpoint idm_flow_endpoint " + "endpoint-left",
        },
        {
          anchor: [
            0,
            0,
            0,
            -1,
            parseFloat(offsetsTopLeft[0]),
            parseFloat(offsetsTopLeft[1]),
          ],
          cssClass:
            module.packageid + "_endpoint idm_flow_endpoint " + "endpoint-top-left",
        },
        // { anchor: "Center", cssClass: module.packageid + "_endpoint idm_flow_endpoint " + "endpoint-center" },
      ];
      this.plumbIns.addEndpoints(module.packageid, endpointList, common);
    },
    /**
     * 连线样式改变
     * @param {*} linkObject
     */
    linkConnectionStyleChange(_linkObject) {
      let that = this;
      const linkFilterList = this.PageSettingData.page.linkList.filter(
        (item) => item.id == _linkObject.id
      );
      if (!linkFilterList.length) {
        return;
      }
      const linkObject = linkFilterList[0];
      let linkPropData = linkObject.propData;
      this.plumbIns.select().each((connection) => {
        if (connection.id == linkObject.connId) {
          if (linkPropData.linkType) {
            let connectorParam = [linkPropData.linkType];
            switch (linkPropData.linkType) {
              case "Bezier":
                connectorParam[1] = {
                  curviness:
                    linkPropData.BezierCurviness || linkPropData.BezierCurviness == 0
                      ? linkPropData.BezierCurviness
                      : 150,
                };
                break;
              case "Straight":
                connectorParam[1] = {
                  stub:
                    linkPropData.StraightStub || linkPropData.StraightStub == 0
                      ? linkPropData.StraightStub
                      : 0,
                  gap:
                    linkPropData.StraightGap || linkPropData.StraightGap == 0
                      ? linkPropData.StraightGap
                      : 0,
                };
                break;
              case "Flowchart":
                connectorParam[1] = {
                  stub:
                    linkPropData.FlowchartStub || linkPropData.FlowchartStub == 0
                      ? linkPropData.FlowchartStub
                      : 30,
                  gap:
                    linkPropData.FlowchartGap || linkPropData.FlowchartGap == 0
                      ? linkPropData.FlowchartGap
                      : 2,
                  alwaysRespectStubs: true,
                  cornerRadius:
                    linkPropData.FlowchartCornerRadius ||
                    linkPropData.FlowchartCornerRadius == 0
                      ? linkPropData.FlowchartCornerRadius
                      : 8,
                  midpoint:
                    linkPropData.FlowchartMidpoint || linkPropData.FlowchartMidpoint == 0
                      ? linkPropData.FlowchartMidpoint
                      : 0,
                };
                break;
              case "StateMachine":
                connectorParam[1] = {
                  curviness:
                    linkPropData.StateMachineCurviness ||
                    linkPropData.StateMachineCurviness == 0
                      ? linkPropData.StateMachineCurviness
                      : 10,
                  margin:
                    linkPropData.StateMachineMargin ||
                    linkPropData.StateMachineMargin == 0
                      ? linkPropData.StateMachineMargin
                      : 5,
                  proximityLimit:
                    linkPropData.StateMachineProximityLimit ||
                    linkPropData.StateMachineProximityLimit == 0
                      ? linkPropData.StateMachineProximityLimit
                      : 80,
                };
                break;
            }
            connection.setConnector(connectorParam);
          }
          let PaintStyle = _.cloneDeep(jsplumbConfig.ins.PaintStyle);
          if (linkPropData.PaintStyleStroke && linkPropData.PaintStyleStroke.hex8) {
            PaintStyle.stroke = linkPropData.PaintStyleStroke.hex8;
          }
          if (linkPropData.PaintStyleStrokeWidth) {
            PaintStyle.strokeWidth = linkPropData.PaintStyleStrokeWidth;
          }
          connection.setPaintStyle(PaintStyle);
          let HoverPaintStyle = _.cloneDeep(jsplumbConfig.ins.HoverPaintStyle);
          if (
            linkPropData.HoverPaintStyleStroke &&
            linkPropData.HoverPaintStyleStroke.hex8
          ) {
            HoverPaintStyle.stroke = linkPropData.HoverPaintStyleStroke.hex8;
          }
          if (linkPropData.HoverPaintStyleStrokeWidth) {
            HoverPaintStyle.strokeWidth = linkPropData.HoverPaintStyleStrokeWidth;
          }
          connection.setHoverPaintStyle(HoverPaintStyle);
          //先全部移除再重新设置
          connection.removeAllOverlays();
          linkPropData.overlayList &&
            linkPropData.overlayList.forEach((overlayItemData) => {
              let overlayParam = { id: overlayItemData.overlayId };
              if (
                overlayItemData.location &&
                overlayItemData.location.split(",").length > 1
              ) {
                const oosp = overlayItemData.location.split(",");
                overlayParam.location = [parseFloat(oosp[0]), parseFloat(oosp[1])];
              } else if (overlayItemData.location) {
                overlayParam.location = parseFloat(overlayItemData.location);
              }
              overlayParam.direction = overlayItemData.direction || 1;
              switch (overlayItemData.type) {
                case "Arrow":
                case "PlainArrow":
                case "Diamond":
                  if (overlayItemData.arrowSize) {
                    const arrowSizeObject = overlayItemData.arrowSize.split(",");
                    if (arrowSizeObject.length > 0) {
                      overlayParam.width = parseFloat(arrowSizeObject[0]);
                      overlayParam.length = parseFloat(arrowSizeObject[0]);
                    }
                    if (arrowSizeObject.length > 1) {
                      overlayParam.length = parseFloat(arrowSizeObject[1]);
                    }
                  }
                  break;
                case "Label":
                  overlayParam.label = overlayItemData.labelText;
                  let labelStyle = {};
                  if (overlayItemData.font) {
                    labelStyle.font = overlayItemData.font;
                  }
                  if (
                    overlayItemData.padding &&
                    overlayItemData.padding.inputVal &&
                    overlayItemData.padding.selectVal
                  ) {
                    labelStyle.padding =
                      overlayItemData.padding.inputVal +
                      overlayItemData.padding.selectVal;
                  }
                  if (overlayItemData.borderWidth) {
                    labelStyle.borderWidth = overlayItemData.borderWidth;
                  }
                  if (overlayItemData.fillStyle && overlayItemData.fillStyle.hex8) {
                    labelStyle.fill = overlayItemData.fillStyle.hex8;
                  }
                  if (overlayItemData.color && overlayItemData.color.hex8) {
                    labelStyle.color = overlayItemData.color.hex8;
                  }
                  if (overlayItemData.borderStyle && overlayItemData.borderStyle.hex8) {
                    labelStyle.borderStyle = overlayItemData.borderStyle.hex8;
                  }
                  overlayParam.labelStyle = labelStyle;
                  overlayParam.cssClass = "idm-overlay-label";
                  break;
                case "Custom":
                  if (
                    overlayItemData.contentFunction &&
                    overlayItemData.contentFunction.length > 0
                  ) {
                    overlayParam.create = function (component) {
                      //这里处理自定义的事件
                      //const d = document.createElement("span")
                      //d.innerHTML = "请预览看效果"
                      return that.commonCustomEventFunHandle(
                        overlayItemData,
                        "contentFunction",
                        { component, overlaySetData: overlayItemData }
                      );
                    };
                  } else {
                    return;
                  }
                  break;
              }
              if (
                overlayItemData.clickFunction &&
                overlayItemData.clickFunction.length > 0
              ) {
                overlayParam.events = {
                  click: (event, overlay) => {
                    //这里处理自定义的事件
                    that.commonCustomEventFunHandle(overlayItemData, "clickFunction", {
                      event,
                      overlay,
                      overlaySetData: overlayItemData,
                    });
                  },
                };
              }
              connection.addOverlay([overlayItemData.type, overlayParam]);
            });
        }
      });
    },
    /**
     * 重连
     */
    reconnectionAllModule() {
      let that = this;
      let existsLink = this.plumbIns.select();
      that.PageSettingData.page.linkList.forEach((linkObject) => {
        let isExists = false;
        try {
          existsLink.each((connection) => {
            if (connection.endpoints && connection.endpoints.length > 1) {
              if (
                connection.endpoints[0].anchor.x == linkObject.sourceEndpoint.x &&
                connection.endpoints[0].anchor.y == linkObject.sourceEndpoint.y &&
                connection.endpoints[1].anchor.x == linkObject.targetEndpoint.x &&
                connection.endpoints[1].anchor.y == linkObject.targetEndpoint.y
              ) {
                isExists = true;
              }
            }
          });
        } catch (error) {}
        if (!isExists) {
          // sourceEndpoint
          let param = {
            // source:linkObject.sourceId,
            // target:linkObject.targetId,
            source: that.getModuleEndpoint(
              linkObject.sourceId,
              linkObject.sourceEndpoint.x,
              linkObject.sourceEndpoint.y
            ),
            target: that.getModuleEndpoint(
              linkObject.targetId,
              linkObject.targetEndpoint.x,
              linkObject.targetEndpoint.y
            ),
            parameters: {
              isreconnection: true,
              linkObjectId: linkObject.id,
            },
          };
          that.plumbIns.connect(param);
          //根据属性去修改样式
          that.linkConnectionStyleChange(linkObject);
        }
      });
    },
    /**
     * 根据组件和坐标获取Endpoint对象
     * @param {*} packageid
     * @param {*} x
     * @param {*} y
     * @returns
     */
    getModuleEndpoint(packageid, x, y) {
      let rtnEndpoint = null;
      this.plumbIns.selectEndpoints({ source: packageid }).each((endpoint) => {
        if (rtnEndpoint) {
          return;
        }
        if (endpoint.anchor.x == x && endpoint.anchor.y == y) {
          rtnEndpoint = endpoint;
        }
      });
      return rtnEndpoint;
    },
    initJsPlumb() {
      let that = this;
      this.plumbIns = jsPlumb.getInstance(jsplumbConfig.ins);
      this.plumbIns.ready(function () {
        that.plumbIns.setContainer("idm_hover_layer_wrap");
      });
      this.plumbIns.bind("connection", function (conn, e) {
        let parameters = conn.connection.getParameters();
        if (parameters && parameters.isreconnection) {
          //代表是重连的，需要设置对应的id等
          let filterList = that.PageSettingData.page.linkList.filter(
            (item) => item.id == parameters.linkObjectId
          );
          if (filterList.length > 0) {
            conn.connection.canvas.id = filterList[0].canvasId;
            conn.connection.id = filterList[0].connId;
          }
          return;
        }
      });
    },
  },
};
</script>

